import { createContext, useState, useContext, useMemo } from 'react'
import { node } from 'prop-types'
import { APP_DEBUG_MODE, TIPOS_PERSONA, toFormData } from '../helpers'

// Sirve para mostrar correctamente el autocompletado
const initStore = {
  appUser:        {},
  tipoPersona:    '',
  newSocio:       new FormData(),
  setTipoPersona: (persona) => { },
  setNewSocio:    (socio) => { },
  mergeNewSocio:  (formData) => { },
  deleteNewSocio: () => { }
}

export const AppContext = createContext(initStore)

// Hook para acceder al state del store, toma referencia de la prop "value" pasada a "AppContext.Provider"
export const useAppContext = () => useContext(AppContext)

const propTypes = {
  children: node.isRequired,
}

/* Funcion Provider para pasar los datos desde la raiz del proyecto (App.js)
 a todos sus componentes hijos (los componentes/vistas del router) */
export function AppContextProvider({ children }) {
  // State: el estado inicial del store, estas propiedades no se deben modificar directamente
  const [state, setState] = useState({
    appUser: {},
    tipoPersona: TIPOS_PERSONA.FISICA,
    newSocio: null,
  })

  // Actions: funciones que modifican el state del store
  const actions = useMemo(() => ({
    setAppUser(authData) {
      setState({
        ...state,
        appUser: authData
      })
    },

    setTipoPersona(persona) {
      const tipoPersona = persona

      setState({
        ...state,
        tipoPersona
      })
    },

    setNewSocio(socio) {
      APP_DEBUG_MODE && console.log(socio)
      const newSocio = state.newSocio || toFormData(socio)
      APP_DEBUG_MODE && console.log('SET newSocio: ', Object.fromEntries([...newSocio.entries()]))
      APP_DEBUG_MODE && console.log('Referencia array ejemplo: ', JSON.parse(Object.fromEntries([...newSocio.entries()]).Referencias))

      setState({ ...state, newSocio })
    },

    mergeNewSocio(formData) {
      /** Categorias:
        Referencias: [],
        Relacionados: [],
        Documentos: [],
        Ingresos: [],
        Gastos: [],
        DatosPersona: {},
        PerfilTrans: {},
        PEP: {},
        ProveedorRecursos: {},
        PropietarioReal: {},
        EmpresaLabora: {},
       */
      const newSocio = state.newSocio || new FormData()

      for (const [name, value] of formData.entries()) {
        newSocio.set(name, value)
      }

      setState({ ...state, newSocio })
    },

    deleteNewSocio() {
      const newSocio = null
      setState({ ...state, newSocio })
    }

  }), [state])

  const store = useMemo(() => ({
    ...state,
    ...actions
  }), [state, actions])

  return (
    <AppContext.Provider value={store}>
      {children}
    </AppContext.Provider>
  )
}

AppContextProvider.propTypes = propTypes
