import { memo } from 'react'
import { string, object, func, bool, oneOf, number } from 'prop-types'
import { Button as MuiButton, IconButton, Badge } from '@material-ui/core'
import { Icon } from './index'
import { withStyles } from '@material-ui/core/styles'

const propTypes = {
  label: string,
  color: oneOf(['default', 'inherit', 'primary', 'secondary']),
  flat: bool,
  icon: string,
  iconSize: number,
  fullWidth: bool,
  submit: bool,
  disabled: bool,
  focusOnTab: bool,
  badgeContent: number,
  onClick: func,
  style: object,
}

const defaultProps = {
  label: '',
  color: 'primary',
  flat: false,
  icon: '',
  iconSize: 16,
  fullWidth: false,
  submit: false,
  disabled: false,
  focusOnTab: true,
  badgeContent: 0,
  onClick: () => {},
  style: {},
}

const StyledBadge = withStyles((theme) => ({
  badge: {
    right: 8,
    top: 0,
    // border: `2px solid ${theme.palette.background.paper}`,
    padding: '0 4px',
    background: theme.palette.primary.light
  },
}))(Badge)

function Button({
  label,
  color,
  flat,
  icon,
  iconSize,
  fullWidth,
  submit,
  onClick,
  style,
  disabled,
  focusOnTab,
  badgeContent,
}) {
  return label ? (
    <MuiButton
      variant={flat ? 'text' : 'contained'}
      color={color || (flat ? 'inherit' : 'primary')}
      type={submit ? 'submit' : 'button'}
      fullWidth={fullWidth}
      onClick={onClick}
      style={{ textTransform: 'none', ...style }}
      startIcon={icon && <Icon className={icon} />}
      disabled={disabled}
      tabIndex={focusOnTab ? 0 : -1}
    >
      {label}
    </MuiButton>
  ) : (
    <IconButton
      color={color || 'inherit'}
      onClick={onClick}
      style={{ ...style, padding: 16 }}
      disabled={disabled}
      tabIndex={focusOnTab ? 0 : -1}
    >
      <StyledBadge badgeContent={badgeContent} variant="standard" overlap="circular" color="primary">
        <Icon className={icon} fontSize={iconSize} />
      </StyledBadge>
    </IconButton>
  )
}

Button.propTypes = propTypes
Button.defaultProps = defaultProps

export default memo(Button)
