import { memo, useEffect } from "react";
import { useFormikContext } from "formik";
import { MyTextInput } from "../formik";
import FileAdd from "./FileAdd";
import MaterialCard from "../materiales/MaterialCard";
import MaterialImage from "../materiales/MaterialImage";

function FormCategoria({ currentCategoria }) {
    const formikProps = useFormikContext();

    const iniciar = (currentCategoria) => {
        formikProps.setFieldValue("id", currentCategoria.id);

        formikProps.setFieldValue("url", currentCategoria.url);

        formikProps.setFieldValue("description", currentCategoria.description);

        formikProps.setFieldValue("title", currentCategoria.title);
    };
    useEffect(() => {
        iniciar(currentCategoria);
    }, [currentCategoria]);

    return (
        <>

        <div className="form-group mb-3 ">
             <MyTextInput
                label="Nombre Categoria"
                name="title"
                id="title"
                type="text"
                placeholder="title"
            />
             </div>



            <div className="form-group mb-3">
            <MyTextInput
                 label="Descripcion"
                 name="description"
                 id="description"
                 type="text"
                 placeholder="Descripcion"
                                />
           </div>

            <div className="form-group mb-3">
                   <FileAdd />
            </div>
            <div className="row rows-cols-1 row-cols-md-3 g-3 animate__animated animate__fadeIn notes__image">
                                {" "}




                    <MaterialCard
                        key={currentCategoria.id}
                        material={currentCategoria}


                    >
                        {
                            ()  => (

                                    <>
                                        <MaterialImage   className="custom-image" style={{ boxShadow: '10px 10px 10px rgba(0,0,0,0.2)' }} />


                                    </>
                                )
                            }
                    </MaterialCard>

                              {" "}
                            </div>
        </>
    );
}
export default memo(FormCategoria);
