import { memo } from 'react'
import { string, array, func, bool } from 'prop-types'
import { NavLink } from 'react-router-dom'
import { makeStyles, List, ListItem, Collapse, ListItemIcon, ListItemText } from '@material-ui/core'
import { Icon, SidebarAccordionTitle } from './index'
import { COLORS } from '../main'
import { routeFormat } from '../helpers/functions'

const propTypes = {
  icon: string,
  open: bool,
  title: string,
  subitems: array,
  titleAsRoute: bool,
  handleClick: func,
  isActive: func,
}

const defaultProps = {
  icon: '',
  open: false,
  title: 'No title',
  subitems: [],
  titleAsRoute: false,
  handleClick: () => {},
  isActive: () => {},
}

const useStyles = makeStyles((theme) => ({
  icon: {
    minWidth: 40,
    color: COLORS.WHITE,
    transition: 'all 0.2s ease-in-out',
    transformOrigin: '6px 8px', // 5 9
  },
  arrowAnim: {
    transform: 'rotate(180deg)',
  },
  nested: {
    paddingLeft: theme.spacing(9),
    transition: '250ms',
    '&:hover': {
      background: COLORS.LIGHTEN,
    }
  },
  item: {
    transition: '250ms',
    '&:hover': {
      background: COLORS.LIGHTEN,
    }
  },
  navLink: {
    color: 'inherit',
    textDecoration: 'none',
    '&.active': {
      color: theme.palette.primary.main,
    },
  },
}))

function SidebarAccordion({ icon, open, title, subitems, handleClick, isActive, titleAsRoute }) {
  const classes = useStyles()

  return (
    <>
      <SidebarAccordionTitle title={title} subitems={subitems} titleAsRoute={titleAsRoute} isActive={isActive}>
        <ListItem button onClick={handleClick}>
          <ListItemIcon>
            <Icon className={`${classes.icon} ${icon}`} fontSize={20} />
          </ListItemIcon>
          <ListItemText primary={title} />
          {!titleAsRoute &&
            <Icon className={`${classes.icon} ${open ? classes.arrowAnim : ''} fas fa-chevron-up`} />}
        </ListItem>
      </SidebarAccordionTitle>

      {subitems?.length ? (
        <Collapse in={open} timeout="auto" unmountOnExit>
          <List component="div" disablePadding style={{ background: 'rgba(0,0,0,0.1)' }}>
            {
              subitems.map((_title) => {
                const subItemRoute = routeFormat(_title)

                return (
                  <NavLink key={subItemRoute} to={`/admin/${subItemRoute}`}  isActive={isActive(`/${subItemRoute}`)} className={classes.navLink}>
                    <ListItem button className={classes.nested}>
                      <ListItemText primary={_title} />
                    </ListItem>
                  </NavLink>
                )
              })
            }
          </List>
        </Collapse>
      ) : null}

    </>
  )
}

SidebarAccordion.propTypes = propTypes
SidebarAccordion.defaultProps = defaultProps

export default memo(SidebarAccordion)
