import { useRef, useEffect, memo, useState, useContext, forwardRef } from 'react'
import { string, node, bool, func, any, object } from 'prop-types'
import { TextField, makeStyles } from '@material-ui/core'
import { FormContext } from './DataForm'
import { isMobile } from '../helpers'

const propTypes = {
  id: string,
  name: string,
  type: string,
  label: string,
  value: any,
  defaultValue: any,
  placeholder: string,
  shrink: bool,
  InputProps: object,
  inputAttrs: object,
  autoFocus: bool,
  error: bool,
  disabled: bool,
  uppercase: bool,
  helperText: string,
  children: node,
  onChange: func,
  onBlur: func,
  onKeyPress: func,
}

const defaultProps = {
  id: undefined,
  name: '',
  type: '',
  label: '',
  value: undefined,
  defaultValue: '',
  placeholder: '',
  shrink: false,
  InputProps: {},
  inputAttrs: {},
  autoFocus: false,
  error: false,
  disabled: false,
  uppercase: true,
  helperText: '',
  children: null,
  onChange: null,
  onBlur: null,
  onKeyPress: null,
}

const useLabelStyles = makeStyles(() => ({
  root: {
    whiteSpace: 'nowrap',
    overflow: 'hidden',
    width: '100%',
    textOverflow: 'ellipsis',
  },
  error: {},
}))

const useMainTextStyles =  makeStyles(() => ({
  root: {
    '& p.MuiFormHelperText-root': {
      position: 'absolute',
      top: '48px',
    },
    // '& ::placeholder': {
    //   textTransform: 'initial'
    // },
    // '& input': {
    //   textTransform: 'uppercase'
    // },
  },
}))

const Input = forwardRef(({
  id,
  name,
  type,
  label,
  value,
  defaultValue,
  placeholder,
  shrink,
  InputProps,
  inputAttrs,
  autoFocus,
  error,
  helperText,
  children,
  onChange,
  onBlur,
  onKeyPress,
  disabled,
  uppercase,
}, ref) => {
  const labelClasses = useLabelStyles()
  const mainClasses = useMainTextStyles()
  const formData = useContext(FormContext)
  const defaultValueFormData = formData instanceof FormData
    ? formData.get(name)
    : formData[name]

  const [inputValue, setInputValue] = useState(defaultValue || defaultValueFormData || '')
  const inputRef = useRef()

  const isShrink = (shrink || ['date', 'time'].includes(type)) ? true : undefined

  const handleChange = (event) => {
    const isSelect = Boolean(children)
    const { value: changeValue } = event.target

    if (onKeyPress) {
      onKeyPress(event)
      return
    }

    if (onChange) {
      onChange(uppercase ? changeValue.toUpperCase() : changeValue)
    }

    if (onBlur) {
      onBlur(changeValue)
    }

    if (value === undefined) {
      setInputValue((uppercase && !isSelect) ? changeValue.toUpperCase() : changeValue)
    }
  }

  const handleKeyPress = (event) => {
    handleChange(event)
  }

  useEffect(() => {
    if (!isMobile() && autoFocus) {
      inputRef.current.focus()
    }
  }, [autoFocus])

  return (
    <TextField
      id={id}
      className={mainClasses.root}
      inputRef={ref || inputRef}
      type={type}
      name={name}
      label={label}
      placeholder={placeholder}
      autoComplete="off"
      select={Boolean(children)}
      value={value === undefined ? inputValue : value}
      onChange={handleChange}
      onKeyPress={handleKeyPress}
      InputProps={InputProps}
      inputProps={inputAttrs}
      InputLabelProps={{ shrink: isShrink, classes: labelClasses }}
      error={error}
      helperText={helperText}
      disabled={disabled}
      fullWidth
    >
      {children}
    </TextField>
  )
})

Input.propTypes = propTypes
Input.defaultProps = defaultProps

export default memo(Input)
