import { useState, useEffect, memo } from "react";
import VideosServie from "../../services/VideosServie";
import { default as Videos } from "./Videos";
import { Modal } from "react-bootstrap";
import { DataGrid } from "@mui/x-data-grid";
import { default as AddVideos } from "./AddVideos";
import { Button } from "@mui/material";
import Swal from "sweetalert2";
import MaterialImage from "../materiales/MaterialImage";
import MaterialCard from "../materiales/MaterialCard";
import { renderCellExpand } from "../formik/renderCellExpand";
import moment from "moment";
import { Header } from "../../views/common";
import useMateriales from "../../hooks/useMateriales";

const VideosList = () => {
    const columns = [
        { field: "id", headerName: "#", width: 70},
        { field: "visible", headerName: "Visible", width: 70 },

        { field: "title", headerName: "Nombre", width: 300,
        renderCell: renderCellExpand },
        {
            field: "created_at",
            headerName: "Fecha de creacion",
            width: 200,
            renderCell: (params) => (
                <p>
                    {" "}
                    {moment(`${params.row.created_at}`)
                        .subtract(10, "days")
                        .calendar()}{" "}
                </p>
            ),
        },
        {
            field: "updated_at",
            headerName: "Ultima actualizacion",
            width: 200,
            renderCell: (params) => (
                <p>
                    {" "}
                    {moment(`${params.row.updated_at}`)
                        .subtract(10, "days")
                        .calendar()}{" "}
                </p>
            ),
        },
        {
            field: "url",
            headerName: "Video",
            width: 200,
            height: 550,
            renderCell: (params) => (
                <>




                {" "}

                    <MaterialCard
                        key={params.row.id}
                        material={params.row}



                    >
                        {
                            ()  => (

                                    <>
                                        <MaterialImage   className="custom-image" style={{ boxShadow: '10px 10px 10px rgba(0,0,0,0.2)' }} />


                                    </>
                                )
                            }
                    </MaterialCard>

                    {" "}



                </>
            ),
        },
        {
            field: "Acciones",
            headerName: "Acciones",
            width: 130,
            height: 550,
            renderCell: (params) => (
                <>
                    <i
                        className="fa-solid fa-eye"
                        onClick={() => setActiveModal(params.row.id)}
                    ></i>
                    <i
                        className="fa-solid fa-trash-can"  style={{color: "red" }}
                        onClick={() => deleteVideo(params.row.id)}
                    ></i>
                </>
            ),
        },
    ];




    const {  videos, setVideos } =
    useMateriales();


    const [currentVideo, setCurrentVideo] = useState(null);
    const [lgShow, setLgShow] = useState(false);
    const [lgShowCrear, setLgShowCrear] = useState(false);

    const retriveVideos = () => {
        VideosServie.getAll()
            .then((response) => {
                setVideos(response.data);
                console.log(response.data);
            })
            .catch((err) => {
                alert("Ocurrio un error");
                console.log(err);
            });
    };

    const setActiveModal = (id) => {
        VideosServie.getById(id)
            .then((response) => {
                setCurrentVideo(response.data);
            })
            .catch((err) => {
                alert("Ocurrio un error");
                console.log(err);
            });

        setLgShow(true);
    };
    const setActiveModalCrear = () => {

        setLgShowCrear(true);
    };
    const setHideModal = () => {
        retriveVideos();
        setLgShow(false);
        setLgShowCrear(false);
    };
    const deleteVideo = (id) => {

        Swal.fire({
            title: 'Are you sure?',
            text: "You won't be able to revert this!",
            icon: 'warning',
            showCancelButton: true,
            confirmButtonColor: '#3085d6',
            cancelButtonColor: '#d33',
            confirmButtonText: 'Yes, delete it!'
          }).then((result) => {
            if (result.isConfirmed) {
              Swal.fire(
                'Deleted!',
                'Your file has been deleted.',
                'success'
              )


        VideosServie.remove(id)
        .then((response) => {
            retriveVideos()
        })
        .catch((err) => {
            alert("Ocurrio un error al tratar de eliminar el video")
            console.log(err)
        })
            }
          })



    };

    return (
        <>

        <Header title="Videos" main>
<Button   onClick={() => setActiveModalCrear()} className="btn btn-primary">
                    Agregar nuevo Video
            </Button>
</Header>


            <Modal
                size="lg"
                show={lgShowCrear}
                onHide={() => setHideModal()}
                backdrop="static"
                keyboard={false}
                aria-labelledby="example-modal-sizes-title-lg"
            >
                <Modal.Header closeButton>
                    <Modal.Title id="example-modal-sizes-title-lg">
                      Registrar video
                    </Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <AddVideos />
                </Modal.Body>
            </Modal>
            <Modal
                size="lg"
                show={lgShow}
                backdrop="static"
                keyboard={false}
                onHide={() => setHideModal()}
                aria-labelledby="example-modal-sizes-title-lg"
            >
                <Modal.Header closeButton>
                    <Modal.Title id="example-modal-sizes-title-lg">
                        Editar Video
                    </Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <Videos id={currentVideo ? currentVideo.id : 0} />
                </Modal.Body>
            </Modal>
            <div style={{ height: 500, width: "100%" }}>
                <DataGrid
                    rows={videos}
                    columns={columns}
                    pageSize={10}
                    rowsPerPageOptions={[5]}
                />
            </div>
        </>
    );
};

export default memo(VideosList);
