import * as React from "react";
import { useMaterial } from '../../hooks/useMaterial';



export const MaterialContext = React.createContext({} as MaterialContextProps);
const { Provider } = MaterialContext;

export interface Material {
    id: number;
    img?: string;
    title?: string;
    url?: string;
    nombre_categoria: string;
    content?: string;
    categoria_id?: number | null;
}
export interface MaterialContextProps {
    material: Material;
    handleClose: () => void;
    handleOpen: (id: number) => void;
    open: boolean;


}


export interface Props {
    material: Material;
    // children?: React.ReactElement | React.ReactElement[];
    children: (args: MaterialCardHandlers) => JSX.Element,
    className?: string;
    style?: React.CSSProperties;


}
export interface MaterialCardHandlers {

    material: Material;


}
const MaterialCard = ({ children, material, className, style }: Props) => {

    const {
        handleClose,
        handleOpen,
        open
    }
        = useMaterial();
      
    return (


        <Provider value={{
            handleClose,
            handleOpen,
            open,
            material

        }}>
                  <div  style={style} className={`${className}`}>
                <div className={`single-product`} >


                {
                      children({
                       material
                    })
                }
                </div>
            </div>

        </Provider>

    );
};
export default React.memo(MaterialCard);
