import { useState, useEffect, memo } from "react";
import MaterialesServie from "../../services/MaterialesServie";
import { default as Materiales } from "./Materiales";
import { Modal } from "react-bootstrap";
import { DataGrid } from "@mui/x-data-grid";
import { default as AddMaterial } from "./AddMaterial";
import { Button } from "@mui/material";
import MaterialImage from "../materiales/MaterialImage";
import MaterialCard from "../materiales/MaterialCard";
import moment from "moment";
import { Header } from "../../views/common";
import { renderCellExpand } from "../formik/renderCellExpand";
import useMateriales from "../../hooks/useMateriales";

const CarruselApp = () => {


    const columns = [
        { field: "id", headerName: "#", width: 70},
        { field: "title", headerName: "Material", width: 300,
        renderCell: renderCellExpand},
        {
            field: "created_at",
            headerName: "Fecha de creacion",
            width: 200,
            renderCell: (params) => (
                <p>
                    {" "}
                    {moment(`${params.row.created_at}`)
                        .subtract(10, "days")
                        .calendar()}{" "}
                </p>
            ),
        },
        {
            field: "updated_at",
            headerName: "Ultima actualizacion",
            width: 200,
            renderCell: (params) => (
                <p>
                    {" "}
                    {moment(`${params.row.updated_at}`)
                        .subtract(10, "days")
                        .calendar()}{" "}
                </p>
            ),
        },
        { field: "nombreCategoria", headerName: "Categoria", width: 200 },

        {
            field: "url",
            headerName: "Imagen",
            width: 200,
            height: 550,
            renderCell: (params) => (
                <MaterialCard
                key={params.row.id}
                material={params.row}
                style={{  margin: '0  auto'  }}

            >
                {
                    ()  => (

                            <>
                                <MaterialImage   className="custom-image" style={{ boxShadow: '10px 10px 10px rgba(0,0,0,0.2)' ,height: '100px'  }} />


                            </>
                        )
                    }
            </MaterialCard>

              ),
        },
        {
            field: "Acciones",
            headerName: "Acciones",
            width: 130,
            height: 550,
            renderCell: (params) => (
                <>
                    <i
                        className="fa-solid fa-eye"
                        onClick={() => setActiveModal(params.row.id)}
                    ></i>
                    <i
                        className="fa-solid fa-trash-can" style={{color: "red" }}
                        onClick={() => deleteMaterial(params.row.id)}
                    ></i>
                </>
            ),
        },
    ];

    const {material, setMaterial } =
    useMateriales();



    const [currentMaterial, setCurrentMaterial] = useState(null);
    const [lgShow, setLgShow] = useState(false);
    const [lgShowCrear, setLgShowCrear] = useState(false);

    const retriveMaterials = () => {
        MaterialesServie.getAll()
            .then((response) => {
                setMaterial(response.data);
                console.log(response.data);
            })
            .catch((err) => {
                alert("Ocurrio un error");
                console.log(err);
            });
    };

    const setActiveModal = (id) => {

            MaterialesServie.getById(id)
                .then((response) => {

                    setCurrentMaterial(response.data);
                })
                .catch((err) => {
                    alert("Ocurrio un error");
                    console.log(err);
                });


        setLgShow(true);
    };
    const setActiveModalCrear = () => {

        setLgShowCrear(true);
    };
    const setHideModal = () => {
        retriveMaterials();
        setLgShow(false);
        setLgShowCrear(false);
    };
    const deleteMaterial = (id) => {
        if (!window.confirm("Seguro que desea eliminar el material")) {
            return;
        }
        MaterialesServie.remove(id)
            .then((response) => {
                retriveMaterials();
            })
            .catch((err) => {
                alert("Ocurrio un error al tratar de eliminar el material");
                console.log(err);
            });
    };

    return (
        <>

<Header title="Materiales" main>
            <button   onClick={() => setActiveModalCrear()} className="btn btn-primary">
            Agregar nuevo Material
            </button>
</Header>



            <Modal
                size="lg"
                show={lgShowCrear}
                backdrop="static"
                keyboard={false}
                onHide={() => setHideModal()}
                aria-labelledby="example-modal-sizes-title-lg"
            >
                <Modal.Header closeButton>
                    <Modal.Title id="example-modal-sizes-title-lg">
                        Registrar Material
                    </Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <AddMaterial />
                </Modal.Body>
            </Modal>
            <Modal
                size="lg"
                show={lgShow}
                backdrop="static"
                keyboard={false}
                onHide={() => setHideModal()}
                aria-labelledby="example-modal-sizes-title-lg"
            >
                <Modal.Header closeButton>
                    <Modal.Title id="example-modal-sizes-title-lg">
                        Editar Material
                    </Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <Materiales id={currentMaterial ? currentMaterial.id : 0} />
                </Modal.Body>
            </Modal>


            <div style={{ height: 800 }}>
                <DataGrid
                    rows={material}
                    columns={columns}
                    pageSize={10}
                    rowHeight={150}
                    rowsPerPageOptions={[5]}

                />
            </div>

        </>
    );
};

export default memo(CarruselApp);
