// https://github.com/caroso1222/notyf
import { Notyf as NotyfLib } from 'notyf'
import 'notyf/notyf.min.css'

const notyfGlobalConfig = {
  duration: 5000,
  position: { x: 'center', y: 'top' },
  dismissible: true,
  types: [
    {
      type: 'success', // Success, abreviado a 's' al utilizar el toast
      background: '#4caf50',
      icon: false,
    },
    {
      type: 'error', // Error, abreviado a 'e' al utilizar el toast
      background: '#f44336',
      icon: false,
    },
    {
      type: 'i', // Info
      background: '#2196f3',
      icon: false,
    },
    {
      type: 'w', // Warning
      background: '#ff9800',
      icon: false,
    },
  ],
}

// Instancia Notyf
const notyf = new NotyfLib(notyfGlobalConfig)

export default () => ({
  /**
   * @param {string} message
   * @param {string} type oneOf(['s', 'e', 'w', 'i'])
   * @param {object} config
   */
  toast: (message, type, config = null) => {
    if (['i', 'w'].includes(type)) {
      return notyf.open({
        type,
        message,
        ...config
      })
    }

    return notyf[(!type || type === 's') ? 'success' : 'error'](config ? { message, ...config } : message)
  },

  dismiss: (notyfInstance) => notyf.dismiss(notyfInstance),

  dismissAll: () => notyf.dismissAll(),
})
