import { memo } from "react";

import MaterialCard from "../components/materiales/MaterialCard";
import MaterialImage from "../components/materiales/MaterialImage";
import useMateriales from "../hooks/useMateriales";

function Nosotros() {
    const { currentNosotros } = useMateriales();
    return (
        <div>
            <section className="bg-light py-5">
                <div className="container">
                    <div className="row align-items-center py-5">
                        <div className="col-md-8 ">
                            {currentNosotros.url != "" ? (
                                <MaterialCard
                                    key={currentNosotros.id}
                                    material={currentNosotros}
                                >
                                    {() => (
                                        <>
                                            <MaterialImage
                                                className="custom-image"
                                                style={{
                                                    boxShadow:
                                                        "10px 10px 10px rgba(0,0,0,0.2)",
                                                }}
                                            />
                                        </>
                                    )}
                                </MaterialCard>
                            ) : (
                                <></>
                            )}
                        </div>

                        <div className="col-md-4 text-normal">
                            <h1>Nosotros</h1>
                            <p>{currentNosotros.descripcion}</p>
                        </div>
                    </div>
                </div>
            </section>
        </div>
    );
}
export default memo(Nosotros);
