import { useState, useEffect, memo } from "react";
import bolsaServie from "../../services/BolsaServie";
import { default as Bolsa } from "./Bolsa";
import { Modal } from "react-bootstrap";
import { DataGrid } from "@mui/x-data-grid";
import moment from "moment";
import { default as AddBolsa } from "./AddBolsa";

import Swal from "sweetalert2";
import MaterialImage from "../materiales/MaterialImage";
import MaterialCard from "../materiales/MaterialCard";
import { Header } from "../../views/common";
import { renderCellExpand } from "../formik/renderCellExpand";
import BolsaServie from "../../services/BolsaServie";
import useMateriales from "../../hooks/useMateriales";

const BolsaList = () => {
    const columns = [
        { field: "id", headerName: "#", width: 70 },
        { field: "visible", headerName: "Visible", width: 70 },

        { field: "title", headerName: "Nombre", width: 500,
        renderCell: renderCellExpand },
        {
            field: "created_at",
            headerName: "Fecha de creacion",
            width: 150,
            renderCell: (params) => (
                <p>
                    {" "}
                    {moment(`${params.row.created_at}`)
                        .subtract(10, "days")
                        .calendar()}{" "}
                </p>
            ),
        },
        {
            field: "updated_at",
            headerName: "Ultima actualizacion",
            width: 150,
            renderCell: (params) => (
                <p>
                    {" "}
                    {moment(`${params.row.updated_at}`)
                        .subtract(10, "days")
                        .calendar()}{" "}
                </p>
            ),
        },
        {
            field: "url",
            headerName: "Imagen",
            width: 150,
            renderCell: (params) => (
                <MaterialCard
                key={params.row.id}
                material={params.row}
                style={{  margin: '0 auto'  }}

            >
                {
                    ()  => (

                            <>
                                <MaterialImage   className="custom-image" style={{ boxShadow: '10px 10px 10px rgba(0,0,0,0.2)' ,height: '100px'  }} />


                            </>
                        )
                    }
            </MaterialCard>

            ),
        },
        {
            field: "Acciones",
            headerName: "Acciones",
            width: 100,

            renderCell: (params) => (
                <>

                    <i className="fa-solid fa-eye" onClick={() => setActiveModal(params.row.id)}></i>

                    <i className="fa-solid fa-trash-can" style={{color: "red" }} onClick={() => deleteBolsa(params.row.id)}></i>

                </>
            ),
        },

    ];

    const {  bolsas, setBolsas } =
    useMateriales();



    const [currentBolsa, setCurrentBolsa] = useState(null);
    const [lgShow, setLgShow] = useState(false);
    const [lgShowCrear, setLgShowCrear] = useState(false);
    const retrivebolsa = () => {
        BolsaServie.getAll()
            .then((response) => {
                setBolsas(response.data);
                console.log(response.data);
            })
            .catch((err) => {
                alert("Ocurrio un error");
                console.log(err);
            });
    };

    const setActiveModal = (id) => {
        BolsaServie.getById(id)
            .then((response) => {
                setCurrentBolsa(response.data);
            })
            .catch((err) => {
                alert("Ocurrio un error");
                console.log(err);
            });

        setLgShow(true);
    };
    const setActiveModalCrear = () => {

        setLgShowCrear(true);
    };
    const setHideModal = () => {
        retrivebolsa();
        setLgShow(false);
        setLgShowCrear(false);
    };

    const deleteBolsa = (id) => {

        Swal.fire({
            title: 'Are you sure?',
            text: "You won't be able to revert this!",
            icon: 'warning',
            showCancelButton: true,
            confirmButtonColor: '#3085d6',
            cancelButtonColor: '#d33',
            confirmButtonText: 'Yes, delete it!'
          }).then((result) => {
            if (result.isConfirmed) {
              Swal.fire(
                'Deleted!',
                'Your file has been deleted.',
                'success'
              )

              BolsaServie.remove(id)
              .then((response) => {
                  retrivebolsa()
              })
              .catch((err) => {
                  alert("Ocurrio un error al tratar de eliminar el Bolsa")
                  console.log(err)
              })
            }
          })



    };

    return (
        <>

<Header title="Bolsa de trabajo" main>
         <button   onClick={() => setActiveModalCrear()} className="btn btn-primary">
                Agregar nuevo Bolsa
            </button>
         </Header>


            <Modal
                size="lg"
                show={lgShowCrear}
                backdrop="static"
                keyboard={false}
                onHide={() => setHideModal()}
                aria-labelledby="example-modal-sizes-title-lg"
            >
                <Modal.Header closeButton>
                    <Modal.Title id="example-modal-sizes-title-lg">
                       Registrar nuevo bolsa de trabajo
                    </Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <AddBolsa />
                </Modal.Body>
            </Modal>
            <Modal
                size="lg"
                show={lgShow}
                backdrop="static"
                keyboard={false}
                onHide={() => setHideModal()}
                aria-labelledby="example-modal-sizes-title-lg"
            >
                <Modal.Header closeButton>
                    <Modal.Title id="example-modal-sizes-title-lg">
                        Editar Bolsa de trabajo
                    </Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <Bolsa id={currentBolsa ? currentBolsa.id : 0} />
                </Modal.Body>
            </Modal>

            <div style={{ height: 500, width: "100%" }}>
                <DataGrid
                    rows={bolsas}
                    columns={columns}
                    pageSize={10}
                    rowHeight={150}
                    rowsPerPageOptions={[5]}
                />
            </div>
        </>
    );
};

export default memo(BolsaList);
