import { useMediaQuery, useTheme } from "@mui/material";
import { memo, useEffect, useMemo, useState } from "react";
import { Button } from "react-bootstrap";
import { useParams, useNavigate } from "react-router-dom";
import { getMaterialById } from "../../selectors/getMaterialById";
import MaterialCard from "./MaterialCard";
import MaterialImage from "./MaterialImage";



const MaterialScreen = () => {
    const { id_material } = useParams();
    console.log(id_material);
    const navigate = useNavigate();
    const [productos, setProductos] = useState([]);
    const material = useMemo(() => getMaterialById( id_material), [id_material]);

    
    useEffect(() => {
    material.then(function(value) {
        console.log(value);
         setProductos(value);
           }, function(reason) {

            console.log(reason);

         });



}, []);

    const handleReturn = () => {
        navigate(-1);
    };

    const { id, title, url, content } = productos;
    const theme = useTheme();
    const isMobile = useMediaQuery(theme.breakpoints.down("md"));
    return (
        <>
     {isMobile ? (


    

            <div className="animate__animated animate__fadeIn">
            <MaterialCard key={id} material={productos}>
                        {() => (
                            <>
                                <MaterialImage
                                    className="custom-image"
                                    datalightbox="productos"
                                    style={{
                                        boxShadow:
                                            "10px 10px 10px rgba(0,0,0,0.2)",
                                        height: "300px",
                                        width: "100%",
                                    }}
                                />
                            </>
                        )}
                    </MaterialCard>
                <h3>{title}</h3>
                <div
                    dangerouslySetInnerHTML={{
                        __html: content,
                    }}
                ></div>

                <Button className="btn btn-outline-info" onClick={handleReturn}>
                    Regresar
                </Button>
            

        </div>
         ) : (

            <div className="row mt-5">

            <div className="col-4">
                <img
                    src={url}
                    alt={title}
                    className="img-thumbnail animate__animated animate__fadeInLeft"
                />
            </div>

            <div className="col-8 animate__animated animate__fadeIn">
                <h3>{title}</h3>
                <div
                    dangerouslySetInnerHTML={{
                        __html: content,
                    }}
                ></div>

                <Button className="btn btn-outline-info" onClick={handleReturn}>
                    Regresar
                </Button>
            </div>

        </div>
            )}
         </>
    );
};
export default memo(MaterialScreen);
