import { memo } from 'react'
import { Avatar, makeStyles } from '@material-ui/core'
import { node } from 'prop-types'
import { GridContainer } from './index'

const useStyles = makeStyles((theme) => ({
  av_sm: {
    width: theme.spacing(3),
    height: theme.spacing(3),
  },
  av_md: {
    width: theme.spacing(6),
    height: theme.spacing(6),
  },
  av_lg: {
    width: theme.spacing(9),
    height: theme.spacing(9),
  },
}))

const propTypes = {
  children: node,
}

const defaultProps = {
  children: [],
}

function AvatarUI({ children, ...avatarProps }) {
  const classes = useStyles()

  return (
    <GridContainer>
      <Avatar {...avatarProps} className={classes.av_lg}>
        { children && children.length ? children.match(/\b(\w)/g).join('') : null }
      </Avatar>
    </GridContainer>
  )
}

AvatarUI.propTypes = propTypes
AvatarUI.defaultProps = defaultProps

export default memo(AvatarUI)
