import React, { useState, useCallback, useEffect, memo } from 'react'
import { useLocation, useNavigate } from 'react-router-dom'
import { Drawer, makeStyles, List, Box, Divider } from '@material-ui/core'
import { Text, SidebarAccordion, Avatar, GridContainer, MenuButton, Progress, Portal } from '../../ui'
import { COLORS } from '../../main'
import {
  useBreakpoint,
  DRAWER_WIDTH,
  SIDEBAR_ITEMS,
  SERVER_RS,
  redirect,
  useApi,
  clearSessionItems,
  getSessionItem,
  replaceLocation
} from '../../helpers'
import { useSelector, useDispatch } from "react-redux"

import { startLogout } from '../../actions/auth'

const useStyles = makeStyles((theme) => ({
  drawer: {
    width: DRAWER_WIDTH,
    flexShrink: 0,
  },
  drawerPaper: {
    width: DRAWER_WIDTH,
    color: COLORS.WHITE,
    background: COLORS.DARK_GREY,
    boxShadow: '0px 2px 4px -1px rgba(0,0,0,0.2), 0px 4px 5px 0px rgba(0,0,0,0.14), 0px 1px 10px 0px rgba(0,0,0,0.12)'
  },
  item: {
    transition: '250ms',
    '&:hover': {
      background: COLORS.LIGHTEN,
    }
  },
  icon: {
    minWidth: 40,
    color: COLORS.WHITE
  },
  divider: {
    background: COLORS.LIGHTEN
  },
  navLink: {
    color: 'inherit',
    textDecoration: 'none',
    '&.active': {
      color: theme.palette.primary.main,
    }
  },
  userInfo: {
    textAlign: 'center',
    display:'flex',
    flexDirection:'column',
    alignItems:'center',
    position: 'relative',
  },
  userInfoOpts: {
    position: 'absolute',
    display: 'flex',
    flexDirection: 'column',
    top: '0',
    right: '0',
  }
}))

function Sidebar() {
    const navigate = useNavigate()
    const dispatch = useDispatch()
  const { pathname: locationPathname } = useLocation() // pathname con slash inicial, ej. "/socio/nuevo/generales"
  const [openDrawer, setOpenDrawer]    = useState(false)
  const [notifCount, setNotifCount]    = useState(0)
  const closeDrawer = () => setOpenDrawer(false)
  const pathname    = locationPathname.split('/')[1] // pathname sin slash inicial y tomando la primera ruta, ej. "socio"
  const classes     = useStyles()
  const smScreen    = useBreakpoint('sm')
  const APP_USER    = getSessionItem('user', {})
  const { name } = useSelector((state) => state.auth);

  const { post: logoutPost, isLoading: logoutLoading, data: logoutData } = useApi('logout')

  useEffect(() => {
    if (logoutData?.Success === SERVER_RS.OK) {
      clearSessionItems()
      replaceLocation('/login')
    }
  }, [logoutData])

  const handleConfigItemClick = (data) => {
    if (data === 'Cerrar sesin') {
      logoutPost('/auth/logout')
    }
  }


  const handleLogout = () => {
    dispatch(startLogout());
};

  // SIDEBAR_ITEMS.LOGOUT.HANDLE_CLICK = () => {
  //   logoutPost('/auth/logout')
  // }

  /* Inicia Manejo de acordeones */
  const checkOpenSectionItems = (sectionItem) =>
    sectionItem.KEYWORDS.some((keyword) => keyword === pathname) // sectionItem.KEYWORDS.join('-').includes(pathname)

  const openAccordionStates = Object.keys(SIDEBAR_ITEMS).map((key) => ({
    [`${SIDEBAR_ITEMS[key].TITLE}`]: checkOpenSectionItems(SIDEBAR_ITEMS[key])
  }))

  /* Comprueba y abre el acordeon correspondiente, seleccionando el item
  del sidebar correspondiente tambien */
  const [open, setOpen] = useState(Object.assign({}, ...openAccordionStates))

  const handleAccordionClick = useCallback((title, titleAsRoute) => () => {
    if (!open[title] && titleAsRoute) {
      setOpen({ [title]: true })
    } else if (!titleAsRoute) {
      setOpen((prevState) => ({ [title]: !prevState[title] }))
    }
  }, [open])

  const isActive = useCallback((route) => () => route.split('/')[1] === pathname, [pathname]) // route.includes(pathname)

  const listItem = (sectionItem, idx) => (
    <React.Fragment key={idx}>
      {sectionItem.DIVIDER && <Divider className={classes.divider} />}
      <SidebarAccordion
        icon={sectionItem.ICON}
        title={sectionItem.TITLE}
        subitems={sectionItem.SUBITEMS}
        isactive={isActive}
        open={open[sectionItem.TITLE]}
        titleAsRoute={sectionItem.TITLE_AS_ROUTE || !sectionItem?.SUBITEMS?.length}
        handleClick={sectionItem.HANDLE_CLICK || handleAccordionClick(sectionItem.TITLE, sectionItem.TITLE_AS_ROUTE)}
      />
    </React.Fragment>
  )

  /* Termina Manejo de acordeones */

  SidebarMemo.toggle = () => setOpenDrawer(!openDrawer)
  // const showSectionFromLandingPage = (sectionItem) =>
  //   sectionItem.ROUTES.includes(getStorageItem('landingPageRoute'))

  return (
    <Drawer
      className={classes.drawer}
      classes={{ paper: classes.drawerPaper }}
      variant={smScreen ? 'temporary' : 'permanent'}
      open={openDrawer}
      onClose={closeDrawer}
    >
      <Box p={2}>
        <Text title={APP_USER.iss} align="center" />
      </Box>
      <Divider className={classes.divider} />

      <Box p={2} className={classes.userInfo}>
        <GridContainer>
          <div grid="grow" />
          <Avatar name="avatarSidebar" src={`${process.env.PUBLIC_URL}/img/logo.jpg`} onClick={() => {}} />
          <Box grid="grow" className={classes.userInfoOpts}>
            <MenuButton
              icon={`${classes.icon} fas fa-bell`}
              iconSize={20}
              badgeContent={notifCount}
              onItemClick={(ref) => {
                setNotifCount(0)
                 navigate( { pathname: '/admin/usuarios'}, { replace: true })
              }}
            />
            {/* <Box py={1} /> */}
            <MenuButton
              icon={`${classes.icon} fas fa-cog`}
              iconSize={20}
              options={['Cerrar sesin']}
              onItemClick={handleLogout}
            />
          </Box>
        </GridContainer>
        <Box pt={2}>
          <Text small={`${ localStorage.getItem('name')  }`} />
        </Box>
      </Box>

      <Divider className={classes.divider} />

      <List>
        {
          Object.keys(SIDEBAR_ITEMS).map((itemKey, i) =>
            listItem(SIDEBAR_ITEMS[itemKey], i))
        }
      </List>

      {logoutLoading && (
        <Portal>
          <Progress fullScreen text="Cerrando sesin..." />
        </Portal>)}
    </Drawer>
  )
}

const SidebarMemo = memo(Sidebar)
SidebarMemo.toggle = () => { }

export default SidebarMemo
