import { memo } from 'react'
import { TableCell, /* withStyles */ } from '@material-ui/core'
import { string, object, func, bool, oneOf } from 'prop-types'
import { isEmptyObj } from '../helpers'
import { /* COLORS */ } from '../main'

const propTypes = {
  label:    string,
  align:    oneOf(['center', 'inherit', 'justify', 'left', 'right']),
  item:     object,
  name:     string,
  getValue: func,
  format:   func,
  small:    bool
}

const defaultProps = {
  label:    '',
  align:    'inherit',
  item:     {},
  name:     '',
  getValue: null,
  format:   null,
  small:    false
}

// const StyledTableCell = withStyles((theme) => ({
//   head: {
//     backgroundColor: COLORS.DARK_GREY,
//     color: COLORS.WHITE,
//   },
//   body: {
//     fontSize: 14,
//   },
// }))(TableCell)

function Column({ label, small, align: alignProp, item, name, format, getValue }) {
  const align = alignProp || (small ? 'center' : 'left')
  const style = { lineHeight: 'initial' }

  if (small) {
    style.width = 100
  }

  const getContent = () => {
    if (isEmptyObj(item)) {
      return label
    }

    const value = getValue
      ? getValue(item)
      : item[name]

    return format
      ? format(value)
      : value
  }

  return (
    <TableCell
      align={align}
      style={style}
    >
      { getContent() }
    </TableCell>
  )
}

Column.propTypes = propTypes
Column.defaultProps = defaultProps

export default memo(Column)
