
import { memo, useContext, useEffect, useState } from 'react';
import { MaterialContext } from './MaterialCard';

export interface Props {
    url?: string;
    title?: string;
    className?: string;
    style?: React.CSSProperties;
    datalightbox?: string;
}


const MaterialImage = ({ url, title, style, className, datalightbox }: Props) => {
    const { material } = useContext(MaterialContext)
    const [video, setVideo] = useState(false)
    useEffect(() => {
        const link = url ? url : material.url ? material.url : "";

        fileValidation(link)
    }, [url]);


    const fileValidation = (link: string) => {

        let allowedExtensions = /(.jpg|.jpeg|.png|.gif)$/i;
        if (!allowedExtensions.exec(link)) {
            if (link != "Noimage") {
                setVideo(true);
            }
            else {
                setVideo(false);

            }
        }
        else {



            setVideo(false);

        }
    }



    return (
        <>

            <div className="product-image"
                style={style}
            >

                {video ? (
                    <>
                        <video
                            loop
                            autoPlay
                            muted
                            width="100%"
                            height="100%"
                        >
                            <source
                                src={url ? url : material.url}
                                type="video/mp4"
                            />
                        </video>
                    </>
                ) : (
                    <>
                        <img
                            className={`${className}`}
                            src={url ? url : material.url ? material.url != "Noimage" ? material.url : process.env.PUBLIC_URL +
                                "/images/Noimage.jpg" : ""} alt="#"  width="100%" 
                                height="100%" />
                        <div className="button">
                            <a
                                href={url ? url : material.url ? material.url != "Noimage" ? material.url : process.env.PUBLIC_URL +
                                    "/images/Noimage.jpg" : ""}
                                data-lightbox={datalightbox ? datalightbox : "roadtrip"}
                                data-title={title ? title : material.title}

                            >
                                <i className="lni lni-cart"></i>{" "}
                                <i className="far fa-eye" />
                            </a>
                        </div>
                    </>
                )
                }

            </div>


        </>
    );
};
export default memo(MaterialImage);
