import { createTheme } from '@material-ui/core/styles'
import { grey, lightGreen, blueGrey } from '@material-ui/core/colors'

const defaultTheme = {
  type: 'light',
  primary: {
    light: lightGreen[500],
    main: lightGreen[700],
    dark: lightGreen[900]
  },
  secondary: {
    light: '#FFFFFF',
    main: '#FFFFFF',
    dark: grey[300]
  },
  background: {
    main: grey[200]
  }
}

const theme = createTheme({
  palette: defaultTheme,
})

const COLORS = {
  DARK_GREY: blueGrey[800],
  LIGHTEN: '#FFF3',
  LIGHTEN2: '#FFF8',
  WHITE: '#FFF'
}

export { theme, COLORS }
