import { useState, useEffect, memo } from "react";
import BlogsServie from "../../services/BlogsServie";
import { Formik, Form } from "formik";
import * as Yup from "yup";
import "../../styles/validaciones.scss";
import Swal from "sweetalert2";
import FormBlog from "./FormBlog";

const Blogs = ({ id }) => {
    const initialMovieState = {
        id: null,
        title: "",
        content: "",
        url: "",
        visible: true
    };

    const [currentBlog, setCurrentBlog] = useState(initialMovieState);

    const getBlog = (id) => {
        BlogsServie.getById(id)
            .then((response) => {
                setCurrentBlog({
                    id: response.data.id || null,
                    title: response.data.title || "",
                    content: response.data.content || "",
                    url: response.data.url || "",
                    visible: response.data.visible || false,
                });
            })
            .catch((err) => {
                alert("Ocurrio un error");
                console.log(err);
            });
    };

    useEffect(() => {
        getBlog(id);
    }, [id]);

    const updateBlog = (data) => {
        BlogsServie.update(currentBlog.id, data)
            .then((response) => {
                Swal.fire({
                    title: "Success!",
                    text: "El Anuncio fue actualizado con xito",
                    icon: "success",
                    confirmButtonText: "OK",


                });
                 getBlog(response.data.id)
                console.log(response.data)
            })
            .catch((err) => {
                alert("Ocurrio un error actualizando el Anuncio");
                console.log(err);
            });
    };

    return (
        <div className="submit-form">
            {!currentBlog ? (
                <div>
                    <h4>Por favor selecciona un registro</h4>
                </div>
            ) : (
                <div>
                    <Formik
                        initialValues={{
                            id: null,
                            title: "",
                            content: "",
                            url: "",
                            visible: true
                        }}
                        onSubmit={(values) => {
                            console.log(values);
                            updateBlog(values);
                        }}
                        validationSchema={Yup.object({
                            title: Yup.string().required("Requerido"),
                            url: Yup.string().required("Requerido"),

                        })}
                    >
                        {(formik) => (
                            <Form>

                                <div className="modal-body">
                                    <FormBlog currentBlog={currentBlog } />
                                </div>
                                <div className="modal-footer">

                                    <button type="submit" className="btn btn-primary">Actualizar</button>
                                </div>
                            </Form>
                        )}
                    </Formik>
                </div>
            )}
        </div>
    );
};

export default memo(Blogs);
