import { memo } from 'react'
import { number, string, node } from 'prop-types'
import { Paper, Box } from '@material-ui/core'
import { Text } from './index'

const propTypes = {
  children: node.isRequired,
  title: string,
  p: number,
}

const defaultProps = {
  title: '',
  p: 0,
}

function Card({ children, title: cardTitle, p, ...props }) {
  return (
    <Paper>
      <Box p={p} position="relative" {...props}>
        { cardTitle &&
          <Box mb={Math.floor(p / 2)} textAlign="center">
            <Text title={cardTitle}  />
          </Box> }
        { children }
      </Box>
    </Paper>
  )
}

Card.propTypes = propTypes
Card.defaultProps = defaultProps

export default memo(Card)
