import { memo, useRef, createContext, forwardRef, useImperativeHandle } from 'react'
import { APP_DEBUG_MODE, toFormData } from '../helpers'
import { func, object, node } from 'prop-types'

export const FormContext = createContext({})

const propTypes = {
  children:    node.isRequired,
  defaultData: object,
  onSubmit:    func
}

const defaultProps = {
  defaultData: {},
  onSubmit:    () => {}
}

function DataForm({ onSubmit, defaultData, children }, ref) {
  const formRef = useRef()

  useImperativeHandle(ref, () => ({
    getData: () => new FormData(formRef.current)
  }))

  const handleSubmit = (event) => {
    event.preventDefault()
    const data = new FormData(formRef.current)
    APP_DEBUG_MODE && console.log('DataForm data: ', Object.fromEntries([...data.entries()]))

    if (onSubmit) {
      onSubmit(data)
    }
  }

  const data = defaultData instanceof FormData
    ? defaultData
    : toFormData(defaultData)

  return (
    <FormContext.Provider value={data}>
      <form ref={formRef} onSubmit={handleSubmit}>
        { children }
      </form>
    </FormContext.Provider>
  )
}

const DataFormWithRef = forwardRef(DataForm)

DataForm.propTypes = propTypes
DataForm.defaultProps = defaultProps

export default memo(DataFormWithRef)
