import { memo, useState } from "react";
import VideosServie from "../../services/VideosServie";
import { Formik, Form } from "formik";
import * as Yup from "yup";
import { MyCheckbox, MyTextInput } from "../formik";

import FileAdd from "./FileAdd";

const AddVideos = () => {
    const [submitted, setSubmitted] = useState(false);

    const saveVideo = (data) => {
        VideosServie.create(data)
            .then((response) => {
                console.log(response.data);
                setSubmitted(true);
            })
            .catch((err) => {
                alert("Ocurrio un error");
                console.log(err);
            });
    };

    const newVideo = () => {
        setSubmitted(false);
    };

    return (
        <div className="submit-form">
            {submitted ? (
                <div>
                    <h4>Has creado exitosamente tu video</h4>
                    <button className="btn btn-primary" onClick={newVideo}>
                        Crear Otra
                    </button>
                </div>
            ) : (
                <div>
                    <Formik
                        initialValues={{
                            id: null,
                            title: "",
                            description: "",
                            url: "",
                            visible: true,

                        }}
                        onSubmit={(values) => {
                            console.log(values);
                            saveVideo(values);
                        }}
                        validationSchema={Yup.object({
                            title: Yup.string().required("Requerido"),
                            url: Yup.string().required("Requerido"),
                        })}
                    >
                        {(formik) => (
                            <Form>
                                <div className="modal-body justify-content">
                                    <div className="form-group mb-3 ">
                                        <MyTextInput
                                            label="Nombre"
                                            name="title"
                                            id="title"
                                            type="text"
                                            placeholder="Nombre de video"
                                        />
                                    </div>

                                    <div className="form-group mb-3">
                                        <MyTextInput
                                            label="Descripcion"
                                            name="description"
                                            id="description"
                                            type="text"
                                            placeholder="Descripcion"
                                        />
                                    </div>

                                    <div className="form-group mb-3">
                                        <FileAdd />
                                    </div>
                                    <div className="form-group mb-3">
                                        <MyCheckbox
                                            label="Visible"
                                            name="visible"
                                            id="visible"
                                        />
                                    </div>
                                </div>
                                <div className="modal-footer">
                                    <button
                                        type="submit"
                                        className="btn btn-primary"
                                    >
                                        {" "}
                                        Guardar{" "}
                                    </button>
                                </div>
                            </Form>
                        )}
                    </Formik>
                </div>
            )}
        </div>
    );
};

export default memo(AddVideos);
