
import { Box, Modal, Typography } from '@mui/material';
import { memo, useContext } from 'react';
import { Link } from "react-router-dom";
import { MaterialContext } from './MaterialCard';

export interface Props {
    className?: string,
    title?: string,
    nombre_categoria?: string,
    activeClass?: string,
    style?: React.CSSProperties,
    id?: number,
    content?: string
}


const MaterialInfo = ({ id, title, nombre_categoria, style, className, content}: Props) => {
    const { handleClose, open, handleOpen, material } = useContext(MaterialContext)
    const styleModal = {
    position: "absolute",
    top: "50%",
    left: "50%",
    transform: "translate(-50%, -50%)",
    width: 400,
    bgcolor: "background.paper",
    border: "2px solid #000",
    boxShadow: 24,
    p: 4,
};

    return (
        <>
            <Modal
                open={open}
                onClose={handleClose}
                aria-labelledby="modal-modal-title"
                aria-describedby="modal-modal-description"
            >
                <Box sx={styleModal}>
                    <Typography
                        id="modal-modal-title"
                        variant="h6"
                        component="h2"
                    >
                       Nombre: {title ? title : material.title}
                    </Typography>
                 <p className={`${className}`} >Categoria: {nombre_categoria ? nombre_categoria : material.nombre_categoria}</p>
                    <Typography id="modal-modal-description" sx={{ mt: 2 }}>
                      Content:  {content ? content : material.content}
                    </Typography>
                </Box>
            </Modal>

            <div className="product-info text-center">
                <p onClick={() => handleOpen(id ? id : material.id)} className={`${className}`} style={style}>
                    {title ? title : material.title}
                </p>
                <p className={`${className}`} >{nombre_categoria ? nombre_categoria : material.nombre_categoria}</p>

                <Link to={`/material/${id ? id : material.id}`} className="btn btn-primary ">
                    {" "}
                    Ms informacin{" "}
                </Link>
            </div>
        </>
    );
};
export default memo(MaterialInfo);
