import { memo } from 'react'
import { string, node, func, number } from 'prop-types'
import { InputAdornment as MuiInputAdornment } from '@material-ui/core'
import { Icon } from './index'

const propTypes = {
  position: string,
  color: string,
  icon: string,
  iconSize: number,
  onClick: func,
  children: node
}

const defaultProps = {
  position: 'end',
  color: 'action',
  icon: 'fas fa-search',
  onClick: () => {},
  iconSize: 14,
  children: null,
}

function InputAdornment({ children, position, onClick, color, icon, iconSize }) {
  return (
    <MuiInputAdornment position={position} onClick={onClick}>
      {
        children || <Icon color={color} className={icon} fontSize={iconSize} />
      }
    </MuiInputAdornment>
  )
}

InputAdornment.propTypes = propTypes
InputAdornment.defaultProps = defaultProps

export default memo(InputAdornment)
