import { useState, useEffect, memo } from "react";
import VideosServie from "../../services/VideosServie";
import { Formik, Form } from "formik";
import * as Yup from "yup";
import "../../styles/validaciones.scss";
import Swal from "sweetalert2";
import FormVideo from "./FormVideo";

const Videos = ({ id }) => {
    const initialMovieState = {
        id: null,
        title: "",
        description: "",
        url: "",
        visible: true,

    };

    const [currentVideo, setCurrentVideo] = useState(initialMovieState);

    const getVideo = (id) => {
        VideosServie.getById(id)
            .then((response) => {
                setCurrentVideo({
                    id: response.data.id || null,
                    title: response.data.title || "",
                    description: response.data.description || "",
                    url: response.data.url || "",
                    visible: response.data.visible || false,

                });
            })
            .catch((err) => {
                alert("Ocurrio un error");
                console.log(err);
            });
    };

    useEffect(() => {
        getVideo(id);
    }, [id]);

    const updateVideo = (data) => {
        VideosServie.update(currentVideo.id, data)
            .then((response) => {
                Swal.fire({
                    title: "Success!",
                    text: "El Video fue actualizado conxito",
                    icon: "success",
                    confirmButtonText: "OK",
                });
                console.log(response.data);
                getVideo(response.data.id);

            })
            .catch((err) => {
                alert("Ocurrio un error actualizando el video");
                console.log(err);
            });
    };

    return (
        <div className="submit-form">
            {!currentVideo ? (
                <div>
                    <h4>Por favor selecciona un video</h4>
                </div>
            ) : (
                <div>
                    <Formik
                        initialValues={{
                            id: null,
                            title: "",
                            description: "",
                            url: "",
                            visible: true,

                        }}
                        onSubmit={(values) => {
                            console.log(values);
                            updateVideo(values);
                        }}
                        validationSchema={Yup.object({
                            title: Yup.string().required("Requerido"),
                            url: Yup.string().required("Requerido"),

                        })}
                    >
                        {(formik) => (
                            <Form>

                                <div className="modal-body">
                                     <FormVideo currentVideo={currentVideo } />
                                </div>
                                <div className="modal-footer">

                                    <button type="submit" className="btn btn-primary">Actualizar</button>
                                </div>
                            </Form>
                        )}
                    </Formik>
                </div>
            )}
        </div>
    );
};

export default memo(Videos);
