const commonProperties = {
  getRoute: {
    get() { return routeFormat(this.NAME) }
  },
  getRoutes: {
    get() {
      if (!this?.SUBITEMS?.length) { return [] }

      return this.SUBITEMS.map((title) =>
        routeFormat(title))
    }
  },
  getKeywords: {
    get() {
      return [
        routeFormat(this.TITLE),
        ...this.ROUTES,
      ]
    }
  }
}

Object.keys(SIDEBAR_ITEMS).forEach((key) => {
  Object.defineProperty(SIDEBAR_ITEMS[key], 'ROUTES', commonProperties.getRoutes)
  Object.defineProperty(SIDEBAR_ITEMS[key], 'KEYWORDS', commonProperties.getKeywords)
})

Object.keys(APP_SECTIONS).forEach((key) => {
  Object.defineProperty(APP_SECTIONS[key], 'ROUTE', commonProperties.getRoute)
})
