import { memo } from 'react'
import { node, bool, func, oneOf } from 'prop-types'
import { Dialog as MuiDialog, Box } from '@material-ui/core'
import { GridContainer } from './index'

const propTypes = {
  children: node.isRequired,
  open: bool,
  onClose: func,
  maxWidth: oneOf(['lg', 'md', 'sm', 'xl', 'xs']),
}

const defaultProps = {
  open: false,
  onClose: () => {},
  maxWidth: 'sm',
}

function Dialog({ children, open, onClose, maxWidth }) {
  return (
    <MuiDialog
      open={open}
      onClose={onClose}
      maxWidth={maxWidth}
      fullWidth
    >
      <Box p={2}>
        <GridContainer defaultGrid="full">
          { children }
        </GridContainer>
      </Box>
    </MuiDialog>
  )
}

Dialog.propTypes = propTypes
Dialog.defaultProps = defaultProps

export default memo(Dialog)
