import { useState, useEffect, memo } from "react";
import MaterialesServie from "../../services/MaterialesServie";
import { Formik, Form } from "formik";
import * as Yup from "yup";
import "../../styles/validaciones.scss";
import Swal from "sweetalert2";
import FormMaterial from "./FormMaterial";

const Materiales = ({ id }) => {
    const initialMovieState = {
        id: null,
        title: "",
        content: "",
        url: "",
        categoria_id: "",
    };

    const [currentMaterial, setCurrentMaterial] = useState(initialMovieState);

const getMaterial = (id) => {
        MaterialesServie.getById(id)
            .then((response) => {
                setCurrentMaterial({
                    id: response.data.id || null,
                    title: response.data.title || "",
                    content: response.data.content || "",
                    url: response.data.url || "",
                    categoria_id: response.data.categoria_id || ""
                });
            })
            .catch((err) => {
                alert("Ocurrio un error");
                console.log(err);
            });
    };

    useEffect(() => {
        getMaterial(id);
    }, [id]);

    const updateMaterial = (data) => {
        MaterialesServie.update(currentMaterial.id, data)
            .then((response) => {
                Swal.fire({
                    title: "Success!",
                    text: "El Material fue actualizado conxito",
                    icon: "success",
                    confirmButtonText: "OK",
                });
                console.log(response.data);
                getMaterial(response.data.id);

            })
            .catch((err) => {
                alert("Ocurrio un error actualizando el material");
                console.log(err);
            });
    };

    return (
        <div className="submit-form">
            {!currentMaterial ? (
                <div>
                    <h4>Por favor selecciona un material</h4>
                </div>
            ) : (
                <div>
                    <Formik
                        initialValues={{
                            id: null,
                            title: "",
                            content: "",
                            url: "",
                            categoria_id: "",
                        }}
                        onSubmit={(values) => {
                            console.log(values);
                            updateMaterial(values);
                        }}
                        validationSchema={Yup.object({
                            title: Yup.string().required("Requerido"),
                            url: Yup.string().required("Requerido"),
                            categoria_id: Yup.string().required("Requerido"),


                        })}
                    >
                        {(formik) => (
                            <Form>

                                <div className="modal-body">
                                <FormMaterial currentMaterial={currentMaterial } />
                                </div>
                                <div className="modal-footer">

                                    <button type="submit" className="btn btn-primary">Actualizar</button>
                                </div>
                            </Form>
                        )}
                    </Formik>
                </div>
            )}
        </div>
    );
};

export default memo(Materiales);
