import { useDispatch } from 'react-redux';
import { Link } from 'react-router-dom'
import { useForm } from '../../hooks/useForm';
import { startRegister } from '../../actions/auth';
import Swal from 'sweetalert2';
import '../../styles/styles.scss';
import { memo } from 'react';
import { Header } from '../../views/common';

const RegisterScreen = () => {
    const dispatch = useDispatch();



    const [ formRegisterValues, handleRegisterInputChange ] = useForm({
        rName: 'Nando',
        rUsername: 'nando@gmail.com',
        rPassword1: '12345678',
        rPassword2: '12345678'
    });
    const { rName, rUsername, rPassword1, rPassword2 } = formRegisterValues;
    const handleRegister = ( e ) => {
        e.preventDefault();

        if ( rPassword1 !== rPassword2 ) {
            return Swal.fire('Error', 'Las contraseas deben de ser iguales','error');
        }
        console.log('?')
        dispatch( startRegister(rName , rUsername, rPassword1, rPassword2 ) );
    }

    return (
        <>
<Header title="Registrar" main>

</Header>
        <section id="signup" className="bk p-80">
            <div className="container-register">
                <h3>
                    <span className="txt-primary">Ferremateriales</span> El
                    Bosque
                </h3>
                <div className="signup-form">
                    <h3>Registrarse</h3>

                    <form onSubmit={ handleRegister } autocomplete={"off"}>
                        <div className="form-group">
                            <input
                                type="text"
                                className="form-control"
                                placeholder="Nombre"
                                name="rName"
                                value={ rName }
                                onChange={ handleRegisterInputChange }
                            />
                        </div>
                        <div className="form-group">
                            <input
                                type="text"
                                className="form-control"
                                placeholder="User name"
                                name="rUsername"
                                value={ rUsername }
                                onChange={ handleRegisterInputChange }
                            />
                        </div>
                        <div className="form-group">
                            <input
                                type="password"
                                className="form-control"
                                placeholder="Contrasea"
                                name="rPassword1"
                                value={ rPassword1 }
                                onChange={ handleRegisterInputChange }
                            />
                        </div>

                        <div className="form-group">
                            <input
                                type="password"
                                className="form-control"
                                placeholder="Repita la contrasea"
                                name="rPassword2"
                                value={ rPassword2 }
                                onChange={ handleRegisterInputChange }
                            />
                        </div>

                        <div className="form-group">
                            <input
                                type="submit"
                                className="btnSubmit"
                                value="Crear cuenta" />
                        </div>
                    </form>
                    <p className="small">
                        <Link to="/login">Ya tienes cuenta</Link>
                    </p>
                </div>
            </div>
        </section>
        </>
    );
};

export default memo(RegisterScreen);
