import CategoriasServie from "../services/CategoriasServie";

export const getAllCategorias = () => {

   const categorias =    CategoriasServie.getAll()
    .then((response) => {

   let ddData = [];
        for (let i = 0; i < response.data.length; i++) {
            console.log(response.data[i]);
            var objeto =   {  value: response.data[i].id , label: response.data[i].title }
ddData.push(objeto);
          }
        console.log( ddData);

        return ddData;
    })
    .catch((err) => {
        alert("Ocurrio un error");
        console.log(err);
    });
console.log(categorias);
return categorias;
}
