import { useState, memo } from 'react'
import { string, array, number, bool } from 'prop-types'
import { makeStyles, Card, Stepper, Step, StepLabel,
  StepButton, Button, Typography, StepContent } from '@material-ui/core'

const propTypes = {
  orientation: string,
  nonLinear: bool,
  steps: array.isRequired,
  activeStep: number.isRequired,
}

const defaultProps = {
  orientation: 'horizontal',
  nonLinear: false,
}

const useStyles = makeStyles((theme) => ({
  root: {
    width: '100%',
    maxWidth: '100%',
  },
  button: {
    // marginTop: theme.spacing(1),
    marginRight: theme.spacing(1),
  },
  actionsContainer: {
    // marginBottom: theme.spacing(2),
  },
  resetContainer: {
    padding: theme.spacing(3),
  },
}))

function NonLinearStepper({ steps, nonLinear, orientation, activeStep: activeStepProp }) {
  const classes = useStyles()
  const [activeStep, setActiveStep] = useState(activeStepProp || 0)

  // const handleReset = () => {
  //   setActiveStep(0);
  //   steps[0].handleRoute();
  // };

  const handleStep = (step) => () => {
    setActiveStep(step.index)
    step.handleRoute()
  }

  const handleNext = (step) => () => {
    setActiveStep((prevActiveStep) => prevActiveStep + 1)
    step.handleRoute()
  }

  const handleBack = (step) => () => {
    setActiveStep((prevActiveStep) => prevActiveStep - 1)
    step.handleRoute()
  }

  const renderSteps = () => steps.map((step, index, arr) => (
    <Step key={`step_${step.label}`}>

      { nonLinear ? (
        <StepButton onClick={handleStep(step)}>
          { step.label }
        </StepButton>
      ) : (
        <StepLabel>
          { step.label }
        </StepLabel>
      ) }

      { !nonLinear && (
      <StepContent>
        <Typography />
        <div className={classes.actionsContainer}>
          <div>
            <Button
              disabled={activeStep === 0}
              onClick={handleBack(arr[index - 1])}
              className={classes.button}
            >
              Atrs
            </Button>

            { activeStep !== steps.length - 1 && (
            <Button
              variant="contained"
              color="primary"
              onClick={handleNext(arr[index + 1])}
              className={classes.button}
            >
              { activeStep === steps.length - 1 ? 'Terminar' : 'Sig.' }
            </Button>)}
          </div>
        </div>
      </StepContent>
      ) }
    </Step>
  ))

  return (
    <Card className={classes.root}>
      <Stepper activeStep={activeStep} orientation={orientation} nonLinear={!!nonLinear}>
        { renderSteps() }
      </Stepper>
      {/* { activeStep === steps.length && (
        <Paper square elevation={0} className={classes.resetContainer}>
          <Typography>All steps completed - you&apos;re finished</Typography>
          <Button onClick={handleReset} className={classes.button}>
            Reset
          </Button>
        </Paper>
      )} */}
    </Card>
  )
}

NonLinearStepper.propTypes = propTypes
NonLinearStepper.defaultProps = defaultProps

export default memo(NonLinearStepper)
