import { Children, memo } from 'react'
import { node, number, oneOf, string } from 'prop-types'
import { Grid } from '@material-ui/core'

const sizes = {
  'md': {
    'sm': { md: 2, sm: 4, xs: 6 },
    'md': { md: 3, sm: 6, xs: 12 },
    'md-gral': { md: 3, sm: 12, xs: 12 },
    'md-lg': { md: 4, sm: 6, xs: 12 },
    'lg': { md: 6, sm: 12, xs: 12 },
    'full': { xs: 12 },
    'item': { xs: 6, sm: 4, md: 4, lg: 3 },
    'grow': { xs: true },
  },
  'sm': {
    'sm': { sm: 3, xs: 6 },
    'md': { sm: 6, xs: 12 },
    'lg': { sm: 12, xs: 12 },
    'grow': { xs: true }
  },
  'xs': {
    'sm': { xs: 6 },
    'md': { xs: 12 },
    'lg': { xs: 12 },
    'grow': { xs: true }
  }
}

const propTypes = {
  children:    node.isRequired,
  spacing:     number,
  direction:   oneOf(['row', 'row-reverse', 'column', 'column-reverse']),
  size:        oneOf(['md', 'sm', 'xs']),
  alignItems:  oneOf(['flex-end', 'flex-start', 'center', 'baseline', 'stretch']),
  justify:     oneOf(['flex-end', 'flex-start', 'center', 'space-between', 'space-around', 'space-evenly']),
  defaultGrid: string,
  className:   string,
  noWrap:      string
}

const defaultProps = {
  spacing:     2,
  direction:   null,
  justify:     null,
  noWrap:      null,
  size:        'md',
  defaultGrid: '',
  className:   '',
  alignItems:  'center',
}

function GridContainer({ children, className, spacing, direction, size, alignItems, justify, defaultGrid, noWrap }) {
  const getSizes = ({ grid = defaultGrid }) => (
    (grid instanceof Object)
      ? grid
      : grid
        ? sizes[size][grid]
        : {}
  )

  return (
    <Grid
      container
      spacing={spacing}
      direction={direction}
      alignItems={alignItems}
      justifyContent={justify}
      className={className}
      wrap={noWrap ? 'nowrap' : null}
    >
      { Children.map(children, (child) => child && (
        child.props.hidden ? child : (
          <Grid item {...getSizes(child.props)}>
            { child }
          </Grid>
        )
      )) }
    </Grid>
  )
}

GridContainer.propTypes = propTypes
GridContainer.defaultProps = defaultProps

export default memo(GridContainer)
