import { memo, useMemo } from 'react'
import { bool, node, oneOf, oneOfType, string } from 'prop-types'
import { Container, Box, makeStyles } from '@material-ui/core'
import { GridContainer } from '../../ui'
import { DRAWER_WIDTH } from '../../helpers'
import Sidebar from './Sidebar'

const propTypes = {
  children: node,
  showSidebar: bool,
  maxWidth: oneOfType([oneOf(['xs', 'sm', 'md', 'lg', 'xl']), string]),
  alignItems: oneOf(['flex-end', 'flex-start', 'center', 'baseline', 'stretch']),
}

const defaultProps = {
  children: [],
  showSidebar: true,
  alignItems: 'flex-start',
  maxWidth: '1600px',
}

const useStyles = makeStyles((theme) => ({
  content: {
    [theme.breakpoints.up('md')]: {
      marginLeft: DRAWER_WIDTH,
      width: `calc(100% - ${DRAWER_WIDTH}px)`,
    }
  },
}))

function Layout({ maxWidth, children, alignItems, showSidebar }) {
  const classes = useStyles()
  const maxW = useMemo(() => (['xs', 'sm', 'md', 'lg', 'xl'].includes(maxWidth)
    ? { maxWidth }
    : new RegExp(['px', '%', 'em', 'rem'].join('|')).test(maxWidth)
      ? { style: { maxWidth } }
      : { style: { maxWidth: defaultProps.maxWidth } }),
  [maxWidth])

  return (
    <Box className="layout">
      {showSidebar && <Sidebar />}

      <Container disableGutters {...maxW}>
        <Box p={2} display="flex" height={alignItems !== 'flex-start' ? '100vh' : '100%'} alignItems={alignItems}>
          <GridContainer defaultGrid="full" className={showSidebar ? classes.content : ''}>
            { children }
          </GridContainer>
        </Box>
      </Container>
    </Box>
  )
}

Layout.propTypes = propTypes
Layout.defaultProps = defaultProps

export default memo(Layout)
