import MaterialesServie from "../services/MaterialesServie";


export const getMaterialById = ( id_material = '' ) => {




    const materiales =  MaterialesServie.getById(id_material)
        .then((response) => {

            return response.data;
        })
        .catch((err) => {
            alert("Ocurrio un error");
            console.log(err);
        });

      return materiales;
}
