import  { memo, useState } from "react";
import CategoriasServie from "../../services/CategoriasServie";
import {Formik, Form } from "formik";
import * as Yup from "yup";
import {  MyTextInput } from "../formik";
import "../../styles/validaciones.scss";
import FileAdd from "./FileAdd";


const AddCategorias = () => {



    const [submitted, setSubmitted] = useState(false);


    const saveCategoria = (data) => {
        CategoriasServie.create(data)
            .then((response) => {
               console.log(response.data);
                setSubmitted(true);
            })
            .catch((err) => {
                alert("Ocurrio un error");
                console.log(err);
            });
    };

    const newCategoria = () => {
        setSubmitted(false);
    };

    return (
        <div className="submit-form">
            {submitted ? (
                <div>
                    <h4>Has creado exitosamente una categoria</h4>
                    <button className="btn btn-primary" onClick={newCategoria}>
                        Crear Otra
                    </button>
                </div>
            ) : (
                <div>

                    <Formik

                        initialValues={{
                            id: null,
                            title: "",
                            description: "",
                            url: "Noimage",
                        }}
                        onSubmit={(values) => {
                            console.log(values);
                            saveCategoria(values);

                        }}
                        validationSchema={Yup.object({
                            title: Yup.string().required("Requerido")

                        })}
                    >


                        {(formik) => (

                            <Form>

                        <div className="modal-body">
                                <div className="form-group mb-3 ">
                                <MyTextInput
                                    label="Nombre"
                                    name="title"
                                    id="title"
                                    type="text"
                                    placeholder="Nombre"
                                />
                               </div>

                                <div className="form-group mb-3 ">
                                <MyTextInput
                                    label="Descripcion"
                                    name="description"
                                    id="description"
                                    type="text"
                                    placeholder="Descripcion"
                                />
                                 </div>

                                 <div className="form-group mb-3 ">
                                    <FileAdd />
                                </div>

                            </div>

                            <div className="modal-footer">

                             <button type="submit" className="btn btn-primary">Guardar</button>
                             </div>
                            </Form>
                        )}
                    </Formik>
                </div>
            )}
        </div>
    );
};

export default memo(AddCategorias);
