import { memo, useEffect, useRef, useState } from "react";
import { useFormikContext } from "formik";
import { MySelect, MyTextInput } from "../formik";
import FileAdd from "./FileAdd";
import CategoriasServie from "../../services/CategoriasServie";
import MaterialCard from "../materiales/MaterialCard";
import MaterialImage from "../materiales/MaterialImage";
import { Editor } from "@tinymce/tinymce-react";

function FormMaterial({ currentMaterial }) {
    const formikProps = useFormikContext();
    const editorRef = useRef(null);
    const iniciar = (currentMaterial) => {
        formikProps.setFieldValue("id", currentMaterial.id);

        formikProps.setFieldValue("url", currentMaterial.url);

        formikProps.setFieldValue("content", currentMaterial.content);

        formikProps.setFieldValue("categoria_id", currentMaterial.categoria_id);

        formikProps.setFieldValue("title", currentMaterial.title);
    };

    const [categorias, setCategorias] = useState([]);
    useEffect(() => {
        retriveBlogs();
    }, []);
    const retriveBlogs = () => {
        CategoriasServie.getAll()
            .then((response) => {
                setCategorias(response.data);
                console.log(response.data);
            })
            .catch((err) => {
                alert("Ocurrio un error");
                console.log(err);
            });
    };
    useEffect(() => {
        iniciar(currentMaterial);
    }, [currentMaterial]);

    return (
        <>

            <div className="form-group mb-3 ">
                    <MyTextInput
                        label="Material"
                        name="title"
                        id="title"
                        type="text"

                    />
            </div>


             <div className="form-group mb-3">
                <MySelect
                    label="Categoria"
                    name="categoria_id"
                    id="categoria_id"
                >
                    <option value="">Selecciona una categoria</option>{" "}
                    {categorias &&
                        categorias.map((categoria, index) => (
                            <option key={index} value={categoria.id}>
                                {categoria.title}
                            </option>
                        ))}
                </MySelect>
            </div>

            <div className="form-group mb-3">
                   <FileAdd />
            </div>
            <div className="form-group mb-3">
                                        <label>Descripcion</label>
                                        <Editor
                                         
                                            tinymceScriptSrc={
                                                process.env.PUBLIC_URL +
                                                "/js/tinymce/js/tinymce/tinymce.min.js"
                                            }
                                            initialValue={currentMaterial.content}
                                            onEditorChange={(stringifiedHtmlValue) => {
                                                formikProps.setFieldValue(
                                                    "content",
                                                    stringifiedHtmlValue
                                                );
                                            }}

                                            onInit={(evt, editor) => (editorRef.current = editor)}

                                            init={{
                                                height: 500,
                                                plugins: [
                                                    "print preview paste importcss searchreplace autolink autosave save directionality code visualblocks visualchars fullscreen image link media template codesample table charmap hr pagebreak nonbreaking anchor toc insertdatetime advlist lists wordcount imagetools textpattern noneditable help charmap quickbars emoticons",
                                                ],

                                                content_style:
                                                    "body { font-family:Helvetica,Arial,sans-serif; font-size:14px }",

                                                    imagetools_cors_hosts: ['picsum.photos'],
                                                    menubar: 'file edit view insert format tools table help',
                                                    toolbar: 'undo redo | bold italic underline strikethrough | fontselect fontsizeselect formatselect | alignleft aligncenter alignright alignjustify | outdent indent |  numlist bullist | forecolor backcolor removeformat | pagebreak | charmap emoticons | fullscreen  preview save print | insertfile image media template link anchor codesample | ltr rtl',
                                                    toolbar_sticky: true,
                                                    autosave_ask_before_unload: false,
                                                    autosave_interval: '30s',
                                                    autosave_prefix: '{path}{query}-{id}-',
                                                    autosave_restore_when_empty: false,
                                                    autosave_retention: '2m',
                                                    image_advtab: true



                                            }}
                                        />
                                    </div>
                            <div className="row rows-cols-1 row-cols-md-3 g-3 animate__animated animate__fadeIn notes__image">
                                {" "}




                    <MaterialCard
                        key={currentMaterial.id}
                        material={currentMaterial}


                    >
                        {
                            ()  => (

                                    <>
                                        <MaterialImage   className="custom-image" style={{ boxShadow: '10px 10px 10px rgba(0,0,0,0.2)' }} />


                                    </>
                                )
                            }
                    </MaterialCard>

                              {" "}
                            </div>

        </>
    );
}
export default memo(FormMaterial);
