import { memo, useState } from "react";
import BolsaServie from "../../services/BolsaServie";
import { Formik, Form } from "formik";
import * as Yup from "yup";
import { MyCheckbox, MyTextInput } from "../formik";

import FileAdd from "./FileAdd";

const AddBolsa = () => {
    const [submitted, setSubmitted] = useState(false);

    const saveBolsa = (data) => {
        BolsaServie.create(data)
            .then((response) => {
                console.log(response.data);
                setSubmitted(true);
            })
            .catch((err) => {
                alert("Ocurrio un error");
                console.log(err);
            });
    };

    const newBolsa = () => {
        setSubmitted(false);
    };

    return (
        <div className="submit-form">
            {submitted ? (
                <div>
                    <h4>Has creado exitosamente tu Bolsa de Trabajo</h4>
                    <button className="btn btn-primary" onClick={newBolsa}>
                        Crear Otra
                    </button>
                </div>
            ) : (
                <div>
                    <Formik
                        initialValues={{
                            id: null,
                            title: "",
                            content: "",
                            url: "",
                            visible: true,
                        }}
                        onSubmit={(values) => {
                            console.log(values);
                            saveBolsa(values);
                        }}
                        validationSchema={Yup.object({
                            title: Yup.string().required("Requerido"),
                            url: Yup.string().required("Requerido"),
                        })}
                    >
                        {(formik) => (
                            <Form>
                                <div className="modal-body">
                                    <div className="form-group mb-3 ">
                                        <MyTextInput
                                            label="Nombre"
                                            name="title"
                                            id="title"
                                            type="text"
                                            placeholder="Nombre trabajo"
                                        />
                                    </div>

                                    <div className="form-group mb-3">
                                        <FileAdd />
                                    </div>
                                    <div className="form-group mb-3">
                                        <MyCheckbox
                                            label="Visible"
                                            name="visible"
                                            id="visible"
                                        />
                                    </div>
                                </div>

                                <div className="modal-footer">
                                    <button
                                        type="submit"
                                        className="btn btn-primary"
                                    >
                                        Guardar
                                    </button>
                                </div>
                            </Form>
                        )}
                    </Formik>
                </div>
            )}
        </div>
    );
};

export default memo(AddBolsa);
