import { memo, useState, useContext } from 'react'
import { any, string, arrayOf, object, bool, func } from 'prop-types'
import { makeStyles } from '@material-ui/core/styles'
import { Select, InputLabel, FormControl, FormHelperText } from '@material-ui/core'
import { FormContext } from './DataForm'

const propTypes = {
  id: string,
  name: string,
  label: string,
  error: bool,
  helperText: string,
  value: any,
  defaultValue: string,
  items: arrayOf(object),
  keyProp: string,
  valueProp: string,
  onChange: func,
  onBlur: func,
}

const defaultProps = {
  id: undefined,
  name: 'UNDEFINED',
  label: 'UNDEFINED',
  error: false,
  helperText: '',
  value: undefined,
  defaultValue: '',
  items: [],
  keyProp: 'DESCRIPCION',
  valueProp: 'ID',
  onChange: null,
  onBlur: null,
}

const useStyles = makeStyles(() => ({
  formControl: {
    display: 'flex',
    '& p.MuiFormHelperText-root': {
      position: 'absolute',
      display: 'flex',
      top: '48px',
    },
  }
}))

function SelectNative({
  id,
  label,
  name,
  keyProp,
  valueProp,
  items,
  value,
  defaultValue,
  helperText,
  error,
  onChange,
  onBlur,
  ...rest
}) {
  const classes = useStyles()

  const formData = useContext(FormContext)
  const defaultValueFormData = formData instanceof FormData
    ? formData.get(name)
    : formData[name]

  const [inputValue, setInputValue] = useState(defaultValue || defaultValueFormData || '')

  const handleChange = (e) => {
    setInputValue(e.target.value)
  }

  return (
    <FormControl className={classes.formControl}>
      <InputLabel htmlFor={`${label}-helper`} error={error}>{label}</InputLabel>
      <Select
        native
        label={label}
        placeholder={label}
        autoComplete="off"
        value={inputValue}
        onChange={handleChange}
        error={error}
        inputProps={{
          name,
          id: `${label}-helper`,
        }}
        {...rest}
      >
        {<option aria-label="None" disabled value=""> </option>}
        {
          items.map((item) =>
            <option key={`${item[keyProp]}_${item[valueProp]}`} value={item[valueProp]}>
              {item[keyProp]}
            </option>)
        }
      </Select>
      <FormHelperText error={error}>{helperText}</FormHelperText>
    </FormControl>
  )
}

SelectNative.propTypes = propTypes
SelectNative.defaultProps = defaultProps

export default memo(SelectNative)
