import { useState, useCallback, useEffect, memo, useMemo, Children, cloneElement } from 'react'
import { node, arrayOf, object, func, bool, string } from 'prop-types'
import {
  TableContainer,
  Table,
  TablePagination,
  TableHead,
  TableBody,
  TableRow,
  TableCell,
  withStyles
} from '@material-ui/core'
import { APP_DEBUG_MODE } from '../helpers'

const propTypes = {
  id: string,
  children: node.isRequired,
  data: arrayOf(object),
  styledRows: bool,
  onRowClick: func,
  onLastPageChange: func,
}

const defaultProps = {
  id: '',
  data: [{}],
  styledRows: false,
  onRowClick: () => {},
  onLastPageChange: () => {},
}

const StyledTableRow = withStyles((theme) => ({
  root: {
    '&:nth-of-type(odd)': {
      backgroundColor: theme.palette.action.hover,
    },
  },
}))(TableRow)

function DataTable({ id, onRowClick, onLastPageChange, children, data: dataProp, styledRows }) {
  const dataPropLen = dataProp.length

  const [{ page, rowsPerPage }, setState] = useState({
    page: 0,
    rowsPerPage: 15
  })

  const handlePageChange = useCallback((_, pageVal) => {
    const hasAHundredRegistries = dataPropLen % 100 === 0
    const nextPageWillOverflow = ((pageVal + 1) * rowsPerPage) > dataPropLen

    if (hasAHundredRegistries && nextPageWillOverflow) {
      onLastPageChange(pageVal + 1)
    }

    setState({ page: pageVal, rowsPerPage })
  }, [rowsPerPage, dataPropLen, onLastPageChange])

  const handleRowsPerPageChange = useCallback((event) => {
    const pageOverflowOnPageChange = ((page + 1) * event.target.value) >= dataPropLen

    setState({ page: (pageOverflowOnPageChange ? 0 : page), rowsPerPage: event.target.value })
  }, [page, dataPropLen])

  useEffect(() => {
    const pageOverflowOnPageChange = ((page + 1) * rowsPerPage) >= dataPropLen
    if (!dataPropLen && pageOverflowOnPageChange) {
      setState({ page: (pageOverflowOnPageChange ? 0 : page), rowsPerPage })
    }
  }, [page, rowsPerPage, dataPropLen])

  const getRowProps = (item) => (onRowClick ? {
    style: { cursor: 'pointer' },
    hover: true,
    onClick: (event) => {
      const { tagName } = event.target
      if (tagName === 'TD') {
        onRowClick(item)
      }
    },
  } : {})

  const renderRows = (item, columns) => {
    const Row = styledRows ? StyledTableRow : TableRow

    return (
      <Row key={`${item.ID}`} {...getRowProps(item)}>
        { columns.map((col) => cloneElement(col, { item })) }
      </Row>
    )
  }

  const columns = useMemo(() => Children.toArray(children), [children])
  const rowsStart = page * rowsPerPage
  const rowsEnd = page * rowsPerPage + rowsPerPage
  const data = useMemo(() => {
    APP_DEBUG_MODE && dataProp.length && console.log(`${id} - data: `, dataProp)
    return (dataProp || [])
  }, [dataProp, id])
  const filteredData = data.slice(rowsStart, rowsEnd)

  return (
    <TableContainer>
      <Table>
        <TableHead>
          <TableRow>
            { columns }
          </TableRow>
        </TableHead>
        <TableBody>

          { filteredData.map((item) => renderRows(item, columns)) }

          { filteredData.length === 0 &&
            <TableRow>
              <TableCell colSpan={columns.length} align="center">
                No hay registros
              </TableCell>
            </TableRow>}

        </TableBody>
      </Table>
      <TablePagination
        rowsPerPageOptions={[15, 30, 50, 100]}
        component="div"
        count={dataPropLen && (dataPropLen % 100 === 0) ? -1 : dataPropLen}
        rowsPerPage={rowsPerPage}
        page={page}
        labelRowsPerPage="Filas"
        labelDisplayedRows={({ from, to }) => `${from} - ${to} de ${dataPropLen}`}
        backIconButtonProps={{ 'aria-label': 'Pgina anterior' }}
        nextIconButtonProps={{ 'aria-label': 'Pgina siguiente' }}
        onChangePage={handlePageChange}
        onChangeRowsPerPage={handleRowsPerPageChange}
      />
    </TableContainer>
  )
}

DataTable.propTypes = propTypes
DataTable.defaultProps = defaultProps

export default memo(DataTable)
