import { memo } from 'react'
import { string, array, func, bool, node } from 'prop-types'
import { NavLink } from 'react-router-dom'
import { makeStyles } from '@material-ui/core'
import { COLORS } from '../main'
import { routeFormat, SIDEBAR_ITEMS } from '../helpers'

const propTypes = {
  title: string,
  subitems: array,
  titleAsRoute: bool,
  isActive: func,
  children: node,
}

const defaultProps = {
  title: 'No title',
  subitems: [],
  titleAsRoute: false,
  isActive: () => {},
  children: [],
}

const useStyles = makeStyles((theme) => ({
  item: {
    transition: '250ms',
    '&:hover': {
      background: COLORS.LIGHTEN,
    }
  },
  navLink: {
    color: 'inherit',
    textDecoration: 'none',
    '&.active': {
      color: theme.palette.primary.main,
    },
  },
}))

function AccordionTitle({ titleAsRoute, isActive, subitems, title, children }) {
  const classes = useStyles()

  const titleRoute = routeFormat(title)
  const sidebarItemsWithOnCLickEvents = Object.keys(SIDEBAR_ITEMS)
    .filter((key) => SIDEBAR_ITEMS[key].HANDLE_CLICK)
    .map((key) => SIDEBAR_ITEMS[key].TITLE)

  return (
    titleAsRoute && !sidebarItemsWithOnCLickEvents.includes(title) ? (
      <NavLink to={`/${titleRoute}`} isactive={isActive(`/${titleRoute}`)} className={classes.navLink}>
        <div className={classes.item}>{children}</div>
      </NavLink>) : <div className={!subitems?.length ? classes.item : null}>{children}</div>
  )
}

AccordionTitle.propTypes = propTypes
AccordionTitle.defaultProps = defaultProps

export default memo(AccordionTitle)
