import { useState, useRef, memo } from 'react'
import { array, string, bool, func, object, oneOf } from 'prop-types'
import { Button, ButtonGroup, ClickAwayListener, Grow, Paper, Popper, MenuItem, MenuList } from '@material-ui/core'
import { Icon } from './index'

const propTypes = {
  options: array,
  startIcon: string,
  style: object,
  flat: bool,
  color: oneOf(['default', 'inherit', 'primary', 'secondary']),
  onItemClick: func,
}

const defaultProps = {
  options: ['Default'],
  startIcon: '',
  style: {},
  flat: false,
  color: 'primary',
  onItemClick: () => {},
}

function SplitButton({ options, startIcon, onItemClick, style, flat, color }) {
  const [open, setOpen] = useState(false)
  const anchorRef = useRef(null)
  const [selectedIndex, setSelectedIndex] = useState(0)

  const handleClick = () => {
    onItemClick(options[selectedIndex])
  }

  const handleMenuItemClick = (event, index) => {
    setSelectedIndex(index)
    setOpen(false)

    onItemClick(options[index])
  }

  const handleToggle = () => {
    setOpen((prevOpen) => !prevOpen)
  }

  const handleClose = (event) => {
    if (anchorRef.current && anchorRef.current.contains(event.target)) {
      return
    }

    setOpen(false)
  }

  return (
    <>
      <ButtonGroup variant="contained" color="primary" ref={anchorRef} aria-label="split button">
        <Button
          onClick={handleClick}
          style={{ textTransform: 'none', ...style }}
          variant={flat ? 'text' : 'contained'}
          color={color || (flat ? 'inherit' : 'primary')}
          startIcon={startIcon && <Icon className={startIcon} fontSize={12} />}
        >
          {options[selectedIndex]}
        </Button>

        <Button
          color="primary"
          size="small"
          aria-controls={open ? 'split-button-menu' : undefined}
          aria-expanded={open ? 'true' : undefined}
          aria-label="select merge strategy"
          aria-haspopup="menu"
          onClick={handleToggle}
        >
          <Icon className="fas fa-caret-down" />
        </Button>
      </ButtonGroup>

      <Popper open={open} anchorEl={anchorRef.current} role={undefined} transition>
        {({ TransitionProps, placement }) => (
          <Grow
            {...TransitionProps}
            style={{
              transformOrigin: placement === 'bottom' ? 'center top' : 'center bottom',
            }}
          >
            <Paper>
              <ClickAwayListener onClickAway={handleClose}>
                <MenuList id="split-button-menu">
                  {options.map((option, index) => (
                    <MenuItem
                      key={option}
                      selected={index === selectedIndex}
                      onClick={(event) => handleMenuItemClick(event, index)}
                    >
                      {option}
                    </MenuItem>
                  ))}
                </MenuList>
              </ClickAwayListener>
            </Paper>
          </Grow>
        )}
      </Popper>
    </>
  )
}

SplitButton.propTypes = propTypes
SplitButton.defaultProps = defaultProps

export default memo(SplitButton)
