import { useState, memo, Children } from 'react'
import { node, array, string } from 'prop-types'
import { withStyles } from '@material-ui/core/styles'
import { Typography } from '@material-ui/core'
import { Icon } from './index'
import MuiAccordion from '@material-ui/core/Accordion'
import MuiAccordionSummary from '@material-ui/core/AccordionSummary'
import MuiAccordionDetails from '@material-ui/core/AccordionDetails'

const propTypes = {
  children: node,
  titles: array,
  openDefault: string,
}

const defaultProps = {
  children: [],
  titles: [],
  openDefault: '',
}

const Accordion = withStyles({
  root: {
    // border: '1px solid rgba(0, 0, 0, .125)',
    // boxShadow: 'none',
    // '&:not(:last-child)': {
    //   borderBottom: 0,
    // },
    // '&:before': {
    //   display: 'none',
    // },
    // '&$expanded': {
    //   margin: 'auto',
    // },
  },
  expanded: {},
})(MuiAccordion)

const AccordionSummary = withStyles({
  root: {
    backgroundColor: 'rgba(0, 0, 0, .03)',
    borderBottom: '1px solid rgba(0, 0, 0, .125)',
    marginBottom: -1,
    minHeight: 56,
    '&$expanded': {
      minHeight: 56,
    },
  },
  content: {
    '&$expanded': {
      margin: '12px 0',
    },
  },
  expanded: {},
})(MuiAccordionSummary)

const AccordionDetails = withStyles((theme) => ({
  root: {
    padding: theme.spacing(2),
  },
}))(MuiAccordionDetails)

function CustomizedAccordions({ children, titles, openDefault }) {
  const [expanded, setExpanded] = useState(openDefault)

  const handleChange = (panel) => (event, newExpanded) => {
    setExpanded(newExpanded ? panel : false)
  }

  return (
    <div>
      {Children.map(children, (child, i) =>
        <Accordion key={`panel-${titles[i]}`} expanded={expanded === titles[i]} onChange={handleChange(titles[i])}>
          <AccordionSummary
            expandIcon={<Icon className="fas fa-chevron-down" />}
            aria-controls={`${titles[i]}-content`}
            id={`${titles[i]}-header`}
          >
            <Typography>{titles[i]}</Typography>
          </AccordionSummary>

          <AccordionDetails>
            {child}
          </AccordionDetails>
        </Accordion>)}
    </div>
  )
}

CustomizedAccordions.propTypes = propTypes
CustomizedAccordions.defaultProps = defaultProps

export default memo(CustomizedAccordions)
