import { useState, useEffect, memo } from "react";
import CategoriasServie from "../../services/CategoriasServie";
import { default as Categorias } from "./Categorias";
import { Modal } from "react-bootstrap";
import { DataGrid } from "@mui/x-data-grid";
import { default as AddCategorias } from "./AddCategorias";
import { Button } from "@mui/material";
import Swal from "sweetalert2";
import MaterialImage from "../materiales/MaterialImage";
import MaterialCard from "../materiales/MaterialCard";
import moment from "moment";
import { Header } from "../../views/common";
import { renderCellExpand } from "../formik/renderCellExpand";
import useMateriales from "../../hooks/useMateriales";

const CategoriasApp = () => {


    const columns = [
        { field: "id", headerName: "#", width: 70 },
        { field: "title", headerName: "Nombre", width: 500,
        renderCell: renderCellExpand},
        {
            field: "created_at",
            headerName: "Fecha de creacion",
            width: 200,
            renderCell: (params) => (
                <p>
                    {" "}
                    {moment(`${params.row.created_at}`)
                        .subtract(10, "days")
                        .calendar()}{" "}
                </p>
            ),
        },
        {
            field: "updated_at",
            headerName: "Ultima actualizacion",
            width: 200,
            renderCell: (params) => (
                <p>
                    {" "}
                    {moment(`${params.row.updated_at}`)
                        .subtract(10, "days")
                        .calendar()}{" "}
                </p>
            ),
        },
        {
            field: "url",
            headerName: "Imagen",
            width: 200,
            height: 550,
            renderCell: (params) => (
                <MaterialCard
                key={params.row.id}
                material={params.row}
                style={{  margin: '0 auto'  }}

            >
                {
                    ()  => (

                            <>
                                <MaterialImage   className="custom-image" style={{ boxShadow: '10px 10px 10px rgba(0,0,0,0.2)' ,height: '100px'  }} />


                            </>
                        )
                    }
            </MaterialCard>

              ),
        },
        {
            field: "Acciones",
            headerName: "Acciones",
            width: 130,
            height: 550,
            renderCell: (params) => (
                <>
                    <i
                        className="fa-solid fa-eye"
                        onClick={() => setActiveModal(params.row.id)}
                    ></i>
                    <i
                        className="fa-solid fa-trash-can" style={{color: "red" }}
                        onClick={() => deleteCategoria(params.row.id)}
                    ></i>
                </>
            ),
        },
    ];


    const { categorias, setCategorias } =
    useMateriales();


    const [currentCategoria, setCurrentCategoria] = useState(null);
    const [lgShow, setLgShow] = useState(false);
    const [lgShowCrear, setLgShowCrear] = useState(false);

    const retriveCategorias = () => {
        CategoriasServie.getAll()
            .then((response) => {
                setCategorias(response.data);
                console.log(response.data);
            })
            .catch((err) => {
                alert("Ocurrio un error");
                console.log(err);
            });
    };

    const setActiveModal = (id) => {

            CategoriasServie.getById(id)
                .then((response) => {

                    setCurrentCategoria(response.data);
                })
                .catch((err) => {
                    alert("Ocurrio un error");
                    console.log(err);
                });


        setLgShow(true);
    };
    const setActiveModalCrear = () => {

        setLgShowCrear(true);
    };
    const setHideModal = () => {
        retriveCategorias();
        setLgShow(false);
        setLgShowCrear(false);
    };
    const deleteCategoria = (id) => {

        Swal.fire({
            title: 'Are you sure?',
            text: "You won't be able to revert this!",
            icon: 'warning',
            showCancelButton: true,
            confirmButtonColor: '#3085d6',
            cancelButtonColor: '#d33',
            confirmButtonText: 'Yes, delete it!'
          }).then((result) => {
            if (result.isConfirmed) {
              Swal.fire(
                'Deleted!',
                'Your file has been deleted.',
                'success'
              )

        CategoriasServie.remove(id)
        .then((response) => {
            retriveCategorias()
        })
        .catch((err) => {
            alert("Ocurrio un error al tratar de eliminar el categoria")
            console.log(err)
        })
            }
          })



    };

    return (
        <>

<Header title="Categorias" main>
  <button   onClick={() => setActiveModalCrear()} className="btn btn-primary">
             Agregar nueva Categoria
            </button>
</Header>


            <Modal
                size="lg"
                show={lgShowCrear}
                onHide={() => setHideModal()}
                backdrop="static"
                keyboard={false}
                aria-labelledby="example-modal-sizes-title-lg"
            >
                <Modal.Header closeButton>
                    <Modal.Title id="example-modal-sizes-title-lg">
                       Registrar Categoria
                    </Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <AddCategorias />
                </Modal.Body>
            </Modal>
            <Modal
                size="lg"
                show={lgShow}
                backdrop="static"
                keyboard={false}
                onHide={() => setHideModal()}
                aria-labelledby="example-modal-sizes-title-lg"
            >
                <Modal.Header closeButton>
                    <Modal.Title id="example-modal-sizes-title-lg">
                        Editar Categoria
                    </Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <Categorias id={currentCategoria ? currentCategoria.id : 0} />
                </Modal.Body>
            </Modal>
            <div style={{ height: 800, width: "100%" }}>
                <DataGrid
                    rows={categorias}
                    columns={columns}
                    pageSize={10}
                    rowsPerPageOptions={[5]}
                    rowHeight={150}
                />
            </div>

        </>
    );
};

export default memo(CategoriasApp);
