import { memo } from 'react'
import { arrayOf, object, string } from 'prop-types'
import { Input } from './index'
import { MenuItem } from '@material-ui/core'

const propTypes = {
  name: string,
  label: string,
  items: arrayOf(object),
  keyProp: string,
  valueProp: string,
}

const defaultProps = {
  name: 'UNDEFINED',
  label: 'UNDEFINED',
  items: [],
  keyProp: 'DESCRIPCION',
  valueProp: 'ID',
}

const Select = ({ keyProp, valueProp, items, ...inputProps }) => (
  <Input {...inputProps}>
    {
      items.map((item) =>
        <MenuItem key={`${item[keyProp]}_${item[valueProp]}`} value={item[valueProp]}>
          {item[keyProp]}
        </MenuItem>)
    }
  </Input>
)

Select.propTypes = propTypes
Select.defaultProps = defaultProps

export default memo(Select)
