import { useState, memo, Children, useCallback, useMemo } from 'react'
import { number, node, array, func } from 'prop-types'
import { Tabs, Tab, Typography, Box, makeStyles, useTheme } from '@material-ui/core'

const propTypesTabPanel = {
  children: node.isRequired,
  value: number,
  index: number,
}

const defaultPropsTabPanel = {
  value: 0,
  index: 0,
}

function TabPanel({ children, value, index, ...remainingTabPanelProps }) {
  return (
    <Typography
      component="div"
      role="tabpanel"
      hidden={value !== index}
      id={`full-width-tabpanel-${index}`}
      aria-labelledby={`full-width-tab-${index}`}
      {...remainingTabPanelProps}
    >
      {value === index && <Box p={3}>{children}</Box>}
    </Typography>
  )
}

TabPanel.propTypes = propTypesTabPanel
TabPanel.defaultProps = defaultPropsTabPanel

const propTypes = {
  children: node,
  labels: array,
  onChange: func,
}

const defaultProps = {
  children: [],
  labels: [],
  onChange: () => {},
}

function a11yProps(index) {
  return {
    id: `full-width-tab-${index}`,
    'aria-controls': `full-width-tabpanel-${index}`,
  }
}

const useStyles = makeStyles((theme) => ({
  root: {
    backgroundColor: theme.palette.background.paper,
    margin: 'auto',
    width: '90%',
  },
}))

const useTabStyles = makeStyles((theme) => ({
  root: {
    backgroundColor: theme.palette.background.paper,
    minWidth: '100px',
    maxWidth: '160px',
    flex: '1 1 0',
  },
}))

function TabsComponent({ labels, onChange, children }) {
  const classes = useStyles()
  const tabClassesRoot = useTabStyles().root
  const theme = useTheme()
  const [value, setValue] = useState(0)

  const handleChange = useCallback((event, newValue) => {
    setValue(newValue)

    onChange && onChange()
  }, [onChange])

  const renderTabs = useMemo(
    () => labels.map((label, i) =>
      <Tab
        className={tabClassesRoot}
        key={`${label}`}
        label={label}
        {...a11yProps(i)}
      />),
    [labels, tabClassesRoot]
  )

  return (
    <>
      { labels.length
        ?
          <Tabs
            value={value}
            onChange={handleChange}
            indicatorColor="primary"
            textColor="primary"
            scrollButtons="on"
            className={classes.root}
            centered
          >
            { renderTabs }
          </Tabs>
        : null}

      {
        Children.map(children, (child, i) =>
          <TabPanel value={value} index={i} dir={theme.direction}>
            { child }
          </TabPanel>)
      }
    </>
  )
}

TabsComponent.propTypes = propTypes
TabsComponent.defaultProps = defaultProps

export default memo(TabsComponent)
