import { useState, useRef, memo } from 'react'
import { array, string, bool, func, object, oneOf, number } from 'prop-types'
import { Button } from './index'
import {
  ClickAwayListener,
  Grow, Paper, Popper,
  MenuItem,
  MenuList,
} from '@material-ui/core'

const propTypes = {
  options: array,
  startIcon: string,
  style: object,
  flat: bool,
  label: string,
  icon: string,
  iconSize: number,
  badgeContent: number,
  color: oneOf(['default', 'inherit', 'primary', 'secondary']),
  onItemClick: func,
}

const defaultProps = {
  options: [],
  startIcon: '',
  style: {},
  flat: false,
  label: '',
  icon: '',
  iconSize: 24,
  badgeContent: 0,
  color: 'primary',
  onItemClick: () => {},
}

function MenuButton({
  options,
  label,
  icon,
  iconSize,
  onItemClick,
  style,
  flat,
  color,
  badgeContent,
}) {
  const anchorRef = useRef(null)
  const [open, setOpen] = useState(false)
  const [selectedIndex, setSelectedIndex] = useState(0)

  const handleClick = () => {
    onItemClick()
  }

  const handleMenuItemClick = (event, index) => {
    setSelectedIndex(index)
    setOpen(false)

    onItemClick(options[index])
  }

  const handleToggle = () => {
    setOpen((prevOpen) => !prevOpen)
  }

  const handleClose = (event) => {
    if (anchorRef.current && anchorRef.current.contains(event.target)) {
      return
    }

    setOpen(false)
  }

  return (
    <>
      <div ref={anchorRef}>
        {/* <ButtonWrapper ref={anchorRef} to={to}> */}
        <Button
          icon={icon}
          iconSize={iconSize}
          label={label}
          style={{ textTransform: 'none', ...style }}
          variant={flat ? 'text' : 'contained'}
          color={color || (flat ? 'inherit' : 'primary')}
          onClick={!options.length ? handleClick : handleToggle}
          badgeContent={badgeContent}
          aria-controls={open ? 'button-menu' : undefined}
          aria-expanded={open ? 'true' : undefined}
          aria-label="select merge strategy"
          aria-haspopup="menu"
        />
        {/* </ButtonWrapper> */}
      </div>

      <Popper style={{ zIndex: 9999 }} open={open} anchorEl={anchorRef.current} role={undefined} transition>
        {({ TransitionProps, placement }) => (
          <Grow
            {...TransitionProps}
            style={{
              transformOrigin: placement === 'bottom' ? 'center top' : 'center bottom',
            }}
          >
            <Paper>
              <ClickAwayListener onClickAway={handleClose}>
                <MenuList id="split-button-menu">
                  {options.map((option, index) => (
                    <MenuItem
                      key={option}
                      selected={index === selectedIndex}
                      onClick={(event) => handleMenuItemClick(event, index)}
                    >
                      {option}
                    </MenuItem>
                  ))}
                </MenuList>
              </ClickAwayListener>
            </Paper>
          </Grow>
        )}
      </Popper>
    </>
  )
}

MenuButton.propTypes = propTypes
MenuButton.defaultProps = defaultProps

export default memo(MenuButton)
