import { useState, useEffect, memo } from "react";
import CategoriasServie from "../../services/CategoriasServie";
import { Formik, Form } from "formik";
import * as Yup from "yup";
import "../../styles/validaciones.scss";
import Swal from "sweetalert2";
import FormCategoria from "./FormCategoria";

const Categorias = ({ id }) => {
    const initialMovieState = {
        id: null,
        title: "",
        description: "",
        url: "",
    };

    const [currentCategoria, setCurrentCategoria] = useState(initialMovieState);

    const getCategoria = (id) => {
        CategoriasServie.getById(id)
            .then((response) => {
                setCurrentCategoria({
                    id: response.data.id || null,
                    title: response.data.title || "",
                    description: response.data.description || "",
                    url: response.data.url || "",
                });
            })
            .catch((err) => {
                alert("Ocurrio un error");
                console.log(err);
            });
    };

    useEffect(() => {
        getCategoria(id);
    }, [id]);

    const updateCategoria = (data) => {
        CategoriasServie.update(currentCategoria.id, data)
            .then((response) => {
                Swal.fire({
                    title: "Success!",
                    text: "La Categoria fue actualizado con xito",
                    icon: "success",
                    confirmButtonText: "OK",
                });
                console.log(response.data);
                getCategoria(response.data.id);

            })
            .catch((err) => {
                alert("Ocurrio un error al actualizar la categoria");
                console.log(err);
            });
    };

    return (
        <div className="submit-form">
            {!currentCategoria ? (
                <div>
                    <h4>Por favor selecciona un categoria</h4>
                </div>
            ) : (
                <div>
                    <Formik
                        initialValues={{
                            id: null,
                            title: "",
                            description: "",
                            url: "Noimage",
                        }}
                        onSubmit={(values) => {
                            console.log(values);
                            updateCategoria(values);
                        }}
                        validationSchema={Yup.object({
                            title: Yup.string().required("Requerido")

                        })}
                    >
                        {(formik) => (
                            <Form>

                                <div className="modal-body">
                                 <FormCategoria currentCategoria={currentCategoria } />
                                </div>
                                <div className="modal-footer">

                                    <button type="submit" className="btn btn-primary">Actualizar</button>
                                </div>
                            </Form>
                        )}
                    </Formik>
                </div>
            )}
        </div>
    );
};

export default memo(Categorias);
