import { memo } from 'react'
import { string, oneOf } from 'prop-types'
import { Typography, Box } from '@material-ui/core'

const propTypes = {
  title: string,
  bold: string,
  label: string,
  small: string,
  color: oneOf([
    'initial', 'inherit', 'primary', 'secondary', 'textPrimary', 'textSecondary', 'error'
  ]),
  align: oneOf(['inherit', 'left', 'center', 'right', 'justify']),
}

const defaultProps = {
  title: '',
  bold: '',
  label: '',
  small: '',
  color: 'initial',
  align: 'inherit',
}

function Text({ title, bold, label, small, color, align }) {
  const style = { lineHeight: '1em' }
  if (bold) style.fontWeight = 500
  if (small) style.opacity = 0.62

  return (
    <Typography
      align={align}
      color={color}
      component="div"
      variant={title ? 'h6' : small ? 'body2' : 'body1'}
      style={style}
    >
      <Box lineHeight="normal">
        { title || bold || label || small }
      </Box>
    </Typography>
  )
}

Text.propTypes = propTypes
Text.defaultProps = defaultProps

export default memo(Text)
