import { FormControl, InputLabel, Select, MenuItem} from '@mui/material'
import {  memo, useEffect, useMemo, useState } from 'react';
import useMateriales from '../../hooks/useMateriales'
import { getAllCategorias } from '../../selectors/getAllCategorias';



const Formulario = () => {
   const ddData = useMemo( () => getAllCategorias(), []);
    const [categorias, setCategorias] = useState([]);

    useEffect(() => {
        ddData.then(function(value) {
            console.log(value);
             setCategorias(value);
               }, function(reason) {

                console.log(reason);

             });

    }, []);




    const { categoria, handleChangeCategoria} = useMateriales()

    return (
        <form>
            <FormControl fullWidth>
                <InputLabel>Categora</InputLabel>
                <Select
                    label="Categora"
                    onChange={handleChangeCategoria}
                    value={categoria}
                >
                     <MenuItem
                            key="0"
                            value="0"
                        >
                            Todas
                        </MenuItem>
                        {categorias.map(categoria => (
                        <MenuItem
                            key={categoria.value}
                            value={categoria.value}
                        >
                            {categoria.label}
                        </MenuItem>
                    ))}
                </Select>
            </FormControl>
        </form>
    )
}

export default memo(Formulario);
