import { useState,  memo } from "react";
import BlogsServie from "../../services/BlogsServie";
import { default as Blogs } from "./Blogs";
import { Modal } from "react-bootstrap";
import { DataGrid } from "@mui/x-data-grid";
import moment from "moment";
import { default as AddBlogs } from "./AddBlogs";
import Swal from "sweetalert2";
import { Header } from "../../views/common";
import MaterialCard from "../materiales/MaterialCard";
import MaterialImage from "../materiales/MaterialImage";
import useMateriales from "../../hooks/useMateriales";
import { renderCellExpand } from "../formik/renderCellExpand";





const BlogsList = () => {

    const columns = [
        { field: "id", headerName: "#", width: 70 },
        { field: "visible", headerName: "Visible", width: 70 },

        {
            field: "title",
            headerName: "Nombre de anuncio",
            width: 300,
            renderCell: renderCellExpand
        },

        {
            field: "created_at",
            headerName: "Fecha de creacion",
            width: 200,
            renderCell: (params) => (
                <p>
                    {" "}
                    {moment(`${params.row.created_at}`)
                        .subtract(10, "days")
                        .calendar()}{" "}
                </p>
            ),
        },
        {
            field: "updated_at",
            headerName: "Ultima actualizacion",
            width: 200,
            renderCell: (params) => (
                <p>
                    {" "}
                    {moment(`${params.row.updated_at}`)
                        .subtract(10, "days")
                        .calendar()}{" "}
                </p>
            ),
        },
        {
            field: "url",
            headerName: "Imagen",
            width: 200,
            renderCell: (params) => (




                    <MaterialCard
                        key={params.row.id}
                        material={params.row}
                        style={{  margin: '0 auto'  }}

                    >
                        {
                            ()  => (

                                    <>
                                        <MaterialImage   className="custom-image" style={{ boxShadow: '10px 10px 10px rgba(0,0,0,0.2)' ,height: '100px'  }} />


                                    </>
                                )
                            }
                    </MaterialCard>

            ),
        },
        {
            field: "Acciones",
            headerName: "Acciones",
            width: 130,

            renderCell: (params) => (
                <>
                    <i
                        className="fa-solid fa-eye"
                        onClick={() => setActiveModal(params.row.id)}
                    ></i>
                    <i style={{color: "red" }}
                        className="fa-solid fa-trash-can"
                        onClick={() => deleteBlog(params.row.id)}
                    ></i>
                </>
            ),
        },
    ];



    const {  blogs, setBlogs } =
    useMateriales();




    const [currentBlog, setCurrentBlog] = useState(null);
    const [lgShow, setLgShow] = useState(false);
    const [lgShowCrear, setLgShowCrear] = useState(false);
    const retriveBlogs = () => {
        BlogsServie.getAll()
            .then((response) => {
                setBlogs(response.data);
                console.log(response.data);
            })
            .catch((err) => {
                alert("Ocurrio un error");
                console.log(err);
            });
    };

    const setActiveModal = (id) => {
        BlogsServie.getById(id)
            .then((response) => {
                setCurrentBlog(response.data);
            })
            .catch((err) => {
                alert("Ocurrio un error");
                console.log(err);
            });

        setLgShow(true);
    };
    const setActiveModalCrear = () => {
        setLgShowCrear(true);
    };
    const setHideModal = () => {
        retriveBlogs();
        setLgShow(false);
        setLgShowCrear(false);
    };

    const deleteBlog = (id) => {
        Swal.fire({
            title: "Are you sure?",
            text: "You won't be able to revert this!",
            icon: "warning",
            showCancelButton: true,
            confirmButtonColor: "#3085d6",
            cancelButtonColor: "#d33",
            confirmButtonText: "Yes, delete it!",
        }).then((result) => {
            if (result.isConfirmed) {
                Swal.fire("Deleted!", "Your file has been deleted.", "success");

                BlogsServie.remove(id)
                    .then((response) => {
                        retriveBlogs();
                    })
                    .catch((err) => {
                        alert(
                            "Ocurrio un error al tratar de eliminar el Anuncio"
                        );
                        console.log(err);
                    });
            }
        });
    };

    return (
        <>
            <Modal
                size="lg"
                show={lgShowCrear}
                backdrop="static"
                keyboard={false}
                onHide={() => setHideModal()}
                aria-labelledby="example-modal-sizes-title-lg"
            >
                <Modal.Header closeButton>
                    <Modal.Title id="example-modal-sizes-title-lg">
                        Registrar Anuncio
                    </Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <AddBlogs />
                </Modal.Body>
            </Modal>
            <Modal
                size="lg"
                show={lgShow}
                backdrop="static"
                keyboard={false}
                onHide={() => setHideModal()}
                aria-labelledby="example-modal-sizes-title-lg"
            >
                <Modal.Header closeButton>
                    <Modal.Title id="example-modal-sizes-title-lg">
                        Editar anuncio
                    </Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <Blogs id={currentBlog ? currentBlog.id : 0} />
                </Modal.Body>
            </Modal>



            <div className="card">
                <div className="card-header">
                <Header title="Anucios" main>
                <button
                            onClick={() => setActiveModalCrear()}
                            className="btn btn-primary"
                        >
                            Agregar nuevo Anuncio
                        </button>
      </Header>
                </div>

                <div className="card-body">
                    <div style={{ height: 800 }}>
                        <DataGrid
                       // rowSpacingType="border"
                       // sx={{ '& .MuiDataGrid-row': { borderTopColor: 'gray', borderTopStyle: 'solid' } }}

                            rows={blogs}
                            rowHeight={150}
                            columns={columns}
                            pageSize={10}
                            rowsPerPageOptions={[5]}
                        />
                    </div>
                </div>
            </div>
        </>
    );
};

export default memo(BlogsList);
