import { memo, useEffect } from 'react'
import { string, bool } from 'prop-types'
import { CircularProgress, makeStyles } from '@material-ui/core'
import { COLORS } from '../main/'
import { DRAWER_WIDTH } from '../helpers/constants'

const propTypes = {
  text: string,
  fullScreen: bool,
  sidebarIsVisible: bool,
}

const defaultProps = {
  text: '',
  fullScreen: false,
  sidebarIsVisible: true,
}

const useStyles = makeStyles((theme) => ({
  wrapper: {
    top: 0,
    bottom: 0,
    right: 0,
    left: 0,
    zIndex: 1200,
    position: 'absolute',
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'center',
    alignItems: 'center',
    background: COLORS.LIGHTEN2,
  },
  text: {
    fontSize: 20,
    color: 'inherit',
  },
  progress: {
    [theme.breakpoints.up('md')]: {
      marginLeft: DRAWER_WIDTH,
      width: `calc(100% - ${DRAWER_WIDTH}px)`,
    },
  }
}))

function Progress({ text, fullScreen, sidebarIsVisible }) {
  const classes = useStyles()

  return (
    <div
      className={`${classes.wrapper} ${(fullScreen || !sidebarIsVisible) || classes.progress}`}
      style={fullScreen ? { position: 'fixed', height: '100%', width: '100%' } : {}}
    >
      <div className={classes.text}>
        {text}
      </div>
      <div>
        <CircularProgress />
      </div>
    </div>
  )
}

Progress.propTypes = propTypes
Progress.defaultProps = defaultProps

export default memo(Progress)
