import { useState, useEffect, memo, useRef } from "react";
import MaterialesServie from "../../services/MaterialesServie";
import { Formik, Form } from "formik";
import * as Yup from "yup";
import { MySelect, MyTextInput } from "../formik";

import FileAdd from "./FileAdd";
import CategoriasServie from "../../services/CategoriasServie";
import { Editor } from "@tinymce/tinymce-react";

const AddMaterial = () => {
    const editorRef = useRef(null);
    const [submitted, setSubmitted] = useState(false);

    const saveMaterial = (data) => {
        MaterialesServie.create(data)
            .then((response) => {
                console.log(response.data);
                setSubmitted(true);
            })
            .catch((err) => {
                alert("Ocurrio un error");
                console.log(err);
            });
    };

    const newMaterial = () => {
        setSubmitted(false);
    };
    const [categoria, setCategorias] = useState([]);
    useEffect(() => {
        retriveBlogs();
    }, []);
    const retriveBlogs = () => {
        CategoriasServie.getAll()
            .then((response) => {
                setCategorias(response.data);
                console.log(response.data);
            })
            .catch((err) => {
                alert("Ocurrio un error");
                console.log(err);
            });
    };
    return (
        <div className="submit-form">
            {submitted ? (
                <div>
                    <h4>Has creado exitosamente tu material</h4>
                    <button className="btn btn-primary" onClick={newMaterial}>
                        Crear Otra
                    </button>
                </div>
            ) : (
                <div>
                    <Formik
                        initialValues={{
                            id: null,
                            title: "",
                            content: "",
                            url: "",
                            categoria_id: "",
                        }}
                        onSubmit={(values) => {
                            console.log(values);
                            saveMaterial(values);
                        }}
                        validationSchema={Yup.object({
                            title: Yup.string().required("Requerido"),
                            url: Yup.string().required("Requerido"),
                            categoria_id: Yup.string().required("Requerido"),
                        })}
                    >
                        {(formik) => (
                            <Form>
                                <div className="modal-body">
                                    <div className="form-group mb-3 ">
                                        <MyTextInput
                                            label="Material"
                                            name="title"
                                            id="title"
                                            type="text"
                                            placeholder="Nombre"
                                        />
                                    </div>

                                    <div className="form-group mb-3">
                                        <MySelect
                                            label="Categoria"
                                            name="categoria_id"
                                            id="categoria_id"
                                        >
                                            <option value="">
                                                Selecciona una categoria
                                            </option>{" "}
                                            {categoria &&
                                                categoria.map(
                                                    (categoria, index) => (
                                                        <option
                                                            key={index}
                                                            value={categoria.id}
                                                        >
                                                            {categoria.title}
                                                        </option>
                                                    )
                                                )}
                                        </MySelect>
                                    </div>

                                    <div className="form-group mb-3">
                                        <FileAdd />
                                    </div>
                                    <div className="form-group mb-3">
                                        <label>Descripcion</label>
                                        <Editor

                                            tinymceScriptSrc={
                                                process.env.PUBLIC_URL +
                                                "/js/tinymce/js/tinymce/tinymce.min.js"
                                            }
                                            id="content"
                                            name="content"
                                            onEditorChange={(
                                                stringifiedHtmlValue
                                            ) => {
                                                formik.setFieldValue(
                                                    "content",
                                                    stringifiedHtmlValue
                                                );
                                            }}

                                            onInit={(evt, editor) =>
                                                (editorRef.current = editor)
                                            }

                                            init={{
                                                height: 500,
                                                plugins: [
                                                    "print preview paste importcss searchreplace autolink autosave save directionality code visualblocks visualchars fullscreen image link media template codesample table charmap hr pagebreak nonbreaking anchor toc insertdatetime advlist lists wordcount imagetools textpattern noneditable help charmap quickbars emoticons",
                                                ],

                                                content_style:
                                                    "body { font-family:Helvetica,Arial,sans-serif; font-size:14px }",

                                                    imagetools_cors_hosts: ['picsum.photos'],
                                                    menubar: 'file edit view insert format tools table help',
                                                    toolbar: 'undo redo | bold italic underline strikethrough | fontselect fontsizeselect formatselect | alignleft aligncenter alignright alignjustify | outdent indent |  numlist bullist | forecolor backcolor removeformat | pagebreak | charmap emoticons | fullscreen  preview save print | insertfile image media template link anchor codesample | ltr rtl',
                                                    toolbar_sticky: true,
                                                    autosave_ask_before_unload: false,
                                                    autosave_interval: '30s',
                                                    autosave_prefix: '{path}{query}-{id}-',
                                                    autosave_restore_when_empty: false,
                                                    autosave_retention: '2m',
                                                    image_advtab: true



                                            }}
                                        />
                                    </div>
                                </div>

                                <div className="modal-footer">
                                    <button
                                        type="submit"
                                        className="btn btn-primary"
                                    >
                                        Guardar
                                    </button>
                                </div>
                            </Form>
                        )}
                    </Formik>
                </div>
            )}
        </div>
    );
};

export default memo(AddMaterial);
