import { memo } from 'react'
import { func, object } from 'prop-types'
import { MenuItem } from '@material-ui/core'
import { GridContainer, DataForm, Input, Button } from '../../ui'
import { getStorageItem, getSessionItem } from '../../helpers'



const propTypes = {
  data: object,
  onSubmit: func,
}

const defaultProps = {
  data: {},
  onSubmit: () => {},
}

const TIPO_SOCIOS = [
  { ID: 1, DESCRIPCION: 'SOCIOS' },
  { ID: 2, DESCRIPCION: 'MENORES' },
  { ID: 3, DESCRIPCION: 'PROSPECTOS' },
  { ID: -1, DESCRIPCION: 'TODOS' },
]

function SearchForm({ data, onSubmit }) {
  const APP_USER   = getSessionItem('user', {})
  const SUCURSALES = getStorageItem('sucursales', [])

  return (
    <DataForm defaultData={data} onSubmit={onSubmit}>
      <GridContainer defaultGrid="md">

        <Input
          name="BUSCAR"
          label="Buscar"
          placeholder="Cdigo o parte del nombre"
          autoFocus
          shrink
        />

        <Input
          name="SUCURSAL"
          label="Sucursal"
          defaultValue={APP_USER.IdSucursal}
        >
          { SUCURSALES.map((suc) =>
            <MenuItem key={suc.IDSUCURSAL} value={suc.IDSUCURSAL}>{suc.SUCURSAL}</MenuItem>) }
        </Input>

        <Input
          name="TIPOSOCIO"
          label="Tipo"
          defaultValue="1"
        >
          { TIPO_SOCIOS.map((tipo) =>
            <MenuItem key={tipo.ID} value={tipo.ID}>{tipo.DESCRIPCION}</MenuItem>) }
        </Input>

        <Button label="Buscar" icon="fas fa-search" submit />
      </GridContainer>
    </DataForm>
  )
}

SearchForm.propTypes = propTypes
SearchForm.defaultProps = defaultProps

export default memo(SearchForm)
