import { memo, useEffect } from "react";
import { useFormikContext } from "formik";
import { MyCheckbox, MyTextInput } from "../formik";
import FileAdd from "./FileAdd";
import MaterialCard from "../materiales/MaterialCard";
import MaterialImage from "../materiales/MaterialImage";

function FormBlog({ currentBlog }) {
    const formikProps = useFormikContext();

    const iniciar = (currentBlog) => {
        formikProps.setFieldValue("id", currentBlog.id);

        formikProps.setFieldValue("url", currentBlog.url);

        formikProps.setFieldValue("content", currentBlog.content);

        formikProps.setFieldValue("title", currentBlog.title);


    };
    useEffect(() => {
        iniciar(currentBlog);
    }, [currentBlog]);

    return (
        <>
            <div className="form-group mb-3 ">
                <MyTextInput
                    label="Nombre"
                    name="title"
                    id="title"
                    type="text"
                    placeholder="Anuncio"
                />
            </div>

            <div className="form-group mb-3">
                <MyTextInput
                    label="Descripcion"
                    name="content"
                    id="content"
                    type="text"
                    placeholder="Descripcion"
                />
            </div>

            <div className="form-group mb-3">
                <FileAdd />
            </div>
            <div className="form-group mb-3">
                <MyCheckbox label="Visible" name="visible" id="visible" />
            </div>

            <div className="row rows-cols-1 row-cols-md-3 g-3 animate__animated animate__fadeIn notes__image">
                {" "}
                <MaterialCard key={currentBlog.id} material={currentBlog}>
                    {() => (
                        <>
                            <MaterialImage
                                className="custom-image"
                                style={{
                                    boxShadow: "10px 10px 10px rgba(0,0,0,0.2)",
                                }}
                            />
                        </>
                    )}
                </MaterialCard>{" "}
            </div>
        </>
    );
}
export default memo(FormBlog);
