import { useState, useEffect, memo } from "react";
import bolsaServie from "../../services/BolsaServie";
import { Formik, Form } from "formik";
import * as Yup from "yup";
import "../../styles/validaciones.scss";
import Swal from "sweetalert2";
import FormBolsa from "./FormBolsa";

const Bolsa = ({ id }) => {
    const initialMovieState = {
        id: null,
        title: "",
        content: "",
        url: "",
        visible: true,

    };

    const [currentBolsa, setCurrentBolsa] = useState(initialMovieState);

    const getBolsa = (id) => {
        bolsaServie.getById(id)
            .then((response) => {
                setCurrentBolsa({
                    id: response.data.id || null,
                    title: response.data.title || "",
                    content: response.data.content || "",
                    url: response.data.url || "",
                    visible: response.data.visible || false,

                });
            })
            .catch((err) => {
                alert("Ocurrio un error");
                console.log(err);
            });
    };

    useEffect(() => {
        getBolsa(id);
    }, [id]);

    const updateBolsa = (data) => {
        bolsaServie.update(currentBolsa.id, data)
            .then((response) => {
                Swal.fire({
                    title: "Success!",
                    text: "El trabajo fue actualizado conxito",
                    icon: "success",
                    confirmButtonText: "OK",
                });
                console.log(response.data);
                getBolsa(response.data.id);

            })
            .catch((err) => {
                alert("Ocurrio un error actualizando el Bolsa");
                console.log(err);
            });
    };

    return (
        <div className="submit-form">
            {!currentBolsa ? (
                <div>
                    <h4>Por favor selecciona una Bolsa de trabajo</h4>
                </div>
            ) : (
                <div>
                    <Formik
                        initialValues={{
                            id: null,
                            title: "",
                            content: "",
                            url: "",
                            visible: true,

                        }}
                        onSubmit={(values) => {
                            console.log(values);
                            updateBolsa(values);
                        }}
                        validationSchema={Yup.object({
                            title: Yup.string().required("Requerido"),
                            url: Yup.string().required("Requerido"),

                        })}
                    >
                        {(formik) => (
                            <Form>

                                <div className="modal-body">
                                 <FormBolsa currentBolsa={currentBolsa } />
                                </div>
                                <div className="modal-footer">

                                    <button type="submit" className="btn btn-primary">Guardar</button>
                                </div>
                            </Form>
                        )}
                    </Formik>
                </div>
            )}
        </div>
    );
};

export default memo(Bolsa);
