import { useReducer, useCallback, useEffect, useMemo } from "react";
import { API_BASE_URL, APP_DEBUG_MODE, SERVER_RS } from "./constants";
import {
  asyncTimeout,
  getSessionItem,
  removeSessionItem,
  toQueryParams,
} from "./functions";

import Notyf from "./Notyf";
import { replaceLocation } from "./utils";

import { useLocation } from "react-router-dom";

function initialState(isLoading = false) {
  return {
    data: null,
    error: null,
    isCompleted: false,
    isLoading,
  };
}

function httpReducer(state, action) {
  switch (action.type) {
    case "SEND":
      return {
        ...initialState(),
        isLoading: true,
      };
    case "SUCCESS":
      return {
        ...initialState(),
        data: action.responseData,
        isCompleted: true,
      };
    case "ERROR":
      return {
        ...initialState(),
        error: action.errorMessage,
        isCompleted: true,
      };
    case "ABORT":
      return initialState();
    default:
      return state;
  }
}

export const useApi  = (strIdentifier = "", startWithLoading = false) => {
  const printDebugLog = useCallback(
    (...message) =>
      APP_DEBUG_MODE &&
      console.log(
        `${strIdentifier ? `useApi(${strIdentifier}): ` : ""}\n`,
        ...message
      ),
    [strIdentifier]
  );

  const controller = useMemo(() => new AbortController(), []);
  const { signal } = controller;

  const [httpState, dispatch] = useReducer(
    httpReducer,
    initialState(startWithLoading)
  );

  const { pathname } = useLocation();
  const request = useCallback(
    async (method, path, body, headers) => {
      // let tokenParam   = ''
      const fullpath = `${API_BASE_URL}${path}`;

      const TOKEN = getSessionItem("jwt");
      const TIMEOUT = 120000;
      const FETCH_OPTS = {
        method,
        signal,
        body,
        headers,
      };

      if (pathname !== "/login") {
        // tokenParam = toQueryParams({ token: TOKEN })
        headers["Content-Type"] = "application/json; charset=UTF-8";
        headers.Authorization = `Bearer ${TOKEN}`;
      }

      // if (tokenParam) {
      //   fullpath += `${fullpath.includes('?') ? '&' : '?'}${tokenParam}`
      // }

      try {
        printDebugLog("[dispatch]: SEND...", fullpath, FETCH_OPTS);
        dispatch({ type: "SEND" });

        const response = await asyncTimeout(
          fetch(fullpath, FETCH_OPTS),
          TIMEOUT
        );
        const json = await response.json();

        if (json?.Success === SERVER_RS.BAD_TOKEN) {
          Notyf().toast(json.Message, "i", {
            position: { x: "right", y: "bottom" },
          });
          removeSessionItem("jwt");
          replaceLocation("/login");
          return;
        }

        printDebugLog("[dispatch]: SUCCESS!");
        dispatch({ type: "SUCCESS", responseData: json });
      } catch (err) {
        printDebugLog(`[dispatch] ERROR: ${err.message}`);
        dispatch({
          type: "ERROR",
          errorMessage: err.message || "Algo sali mal!",
        });

        if (err.message.includes("NetworkError")) {
          Notyf().toast("Error de red al intentar obtener el recurso.", "e");
        }
      }
    },
    [printDebugLog, signal, pathname]
  );

  const abortFetching = useCallback(() => {
    printDebugLog("[dispatch]: ABORT");
    dispatch({ type: "ABORT" });

    controller.abort();
  }, [controller, printDebugLog]);

  // const creds = Buffer.from('ADMIN:a').toString('base64')
  // const headers = { 'headers': { 'Content-Type': 'application/json', 'Autorization': `BASIC ${Buffer.from('ADMIN:a').toString('base64')}` } }
  useEffect(
    () => () => {
      /* Abortar peticin fetch si se desmonta el componente donde se usa el hook */
      abortFetching();
    },
    [abortFetching]
  );

  return {
    ...httpState,
    abortFetching,
    post: useCallback(
      (path, body = {}, headers = {}) =>
        request("POST", path, JSON.stringify(body), headers),
      [request]
    ),
    get: useCallback(
      (path, headers = {}) => request("GET", path, null, headers),
      [request]
    ),
  };
}

export default useApi;
