import { memo } from 'react'
import { node, string, bool } from 'prop-types'
import { GridContainer, Text, Button } from '../../ui/'
import { Sidebar } from '../common'
import { Box } from '@material-ui/core'
import { useBreakpoint } from '../../helpers/'

const propTypes = {
  title: string,
  children: node,
  main: bool,
}

const defaultProps = {
  title: '',
  children: [],
  main: false,
}

function Header({ title, children, main }) {
  const smScreen = useBreakpoint('sm')

  return (
    <GridContainer>
      {(main && smScreen) &&
        <Box m={-1}>
          <Button color="inherit" icon="fas fa-bars" onClick={() => Sidebar.toggle()} />
        </Box>}
      <Text title={title} grid="grow" />
      <GridContainer>
        {children}
      </GridContainer>
    </GridContainer>
  )
}

Header.propTypes = propTypes
Header.defaultProps = defaultProps

export default memo(Header)
