import { memo, useState } from "react";
import BlogsServie from "../../services/BlogsServie";
import { Formik, Form } from "formik";
import * as Yup from "yup";
import { MyCheckbox, MyTextInput } from "../formik";

import FileAdd from "./FileAdd";

const AddBlogs = () => {

    const [submitted, setSubmitted] = useState(false);

    const saveBlog = (data) => {
        BlogsServie.create(data)
            .then((response) => {
                console.log(response.data);
                setSubmitted(true);
            })
            .catch((err) => {
                alert("Ocurrio un error");
                console.log(err);
            });
    };

    const newBlog = () => {
        setSubmitted(false);
    };

    return (
        <div className="submit-form">
            {submitted ? (
                <div>
                    <h4>Se ha creado exitosamente</h4>
                    <button className="btn btn-primary" onClick={newBlog}>
                        Crear Otro
                    </button>
                </div>
            ) : (
                <div>

                    <Formik
                        initialValues={{
                            id: null,
                            title: "",
                            content: "",
                            url: "",
                            visible: true
                        }}
                        onSubmit={(values) => {
                            console.log(values);
                            saveBlog(values);
                        }}
                        validationSchema={Yup.object({
                            title: Yup.string().required("Requerido"),
                            url: Yup.string().required("Requerido"),
                        })}
                    >
                        {(formik) => (
                            <Form>
                        <div className="modal-body">
                                <div className="form-group mb-3 ">
                                        <MyTextInput
                                            label="Nombre"
                                            name="title"
                                            id="title"
                                            type="text"
                                            placeholder="Anuncio"
                                        />
                                </div>

                                <div className="form-group mb-3">
                                <MyTextInput
                                    label="Descripcion"
                                    name="content"
                                    id="content"
                                    type="text"
                                    placeholder="Descripcion"
                                />

                                </div>

                                <div className="form-group mb-3">
                                     <FileAdd />
                                </div>
                                <div className="form-group mb-3">
                                <MyCheckbox
                                    label="Visible"
                                    name="visible"
                                    id="visible"

                                />
                                </div>



                         </div>
                               <div className="modal-footer">

                                <button type="submit" className="btn btn-primary">Guardar</button>
                                </div>


                            </Form>
                        )}
                    </Formik>
                </div>
            )}
        </div>
    );
};

export default memo(AddBlogs);
