import { memo, useEffect } from "react";
import { useFormikContext } from "formik";
import { MyCheckbox, MyTextInput } from "../formik";
import FileAdd from "./FileAdd";
import MaterialCard from "../materiales/MaterialCard";
import MaterialImage from "../materiales/MaterialImage";

function FormVideo({ currentVideo }) {
    const formikProps = useFormikContext();

    const iniciar = (currentVideo) => {
        formikProps.setFieldValue("id", currentVideo.id);

        formikProps.setFieldValue("url", currentVideo.url);

        formikProps.setFieldValue("description", currentVideo.description);

        formikProps.setFieldValue("title", currentVideo.title);
    };
    useEffect(() => {
        iniciar(currentVideo);
    }, [currentVideo]);

    return (
        <>

            <div className="form-group mb-3 ">
            <MyTextInput
                label="Nombre video"
                name="title"
                id="title"
                type="text"
                placeholder="Nombre"
            />
             </div>



            <div className="form-group mb-3">
            <MyTextInput
                label="Descripcion"
                name="description"
                id="description"
                type="text"
                placeholder="Descripcion"
            />

            </div>

            <div className="form-group mb-3">
                   <FileAdd />
            </div>
            <div className="form-group mb-3">
                <MyCheckbox label="Visible" name="visible" id="visible" />
            </div>

            <div className="row rows-cols-1 row-cols-md-3 g-3 animate__animated animate__fadeIn notes__image">
                                {" "}




                    <MaterialCard
                        key={currentVideo.id}
                        material={currentVideo}



                    >
                        {
                            ()  => (

                                    <>
                                        <MaterialImage   className="custom-image" style={{ boxShadow: '10px 10px 10px rgba(0,0,0,0.2)' }} />


                                    </>
                                )
                            }
                    </MaterialCard>

                              {" "}
                            </div>
        </>
    );
}
export default memo(FormVideo);
